import React, { useState, useEffect, useRef } from 'react'
import { useHistory } from 'react-router-dom'

import Bracael from '../Bracael'
import { SnapshotCart } from '../contexts/App'

import ComponentOnCart from './ComponentOnCart'
import ComponentAwaitProductThumb from './ComponentAwaitProductThumb'
import ComponentProductAdditional from './ComponentProductAdditional'
import ComponentProductPick from './ComponentProductPick'
import ComponentProductButtons from './ComponentProductButtons'
import ComponentProductThumb404 from './ComponentProductThumb404'

import {
  Content,
  Thumb,
  Back,
  Sticky,
  Footer,
  Title,
  Name,
  Prices,
  Remove,
  More,
  Description,
  Comment,
  Textarea,
  Comprar,
  DFooter,
  Price,
  Old,
  OFF
} from '../styles/Product'

const { jQuery: $ } = window

function ComponentProductId({ data, id }) {
  const useStatus = useRef(false)
  const useSucessLayout = useRef(null)
  const { useCart, setCart } = SnapshotCart()

  const history = useHistory()
  // eslint-disable-next-line no-unused-vars
  const {
    category,
    description,
    grams,
    price,
    thumb = [],
    title,
    weight,
    extra = {},
    pick = {},
    price_off = 0
  } = data
  const [useMoment, setMoment] = useState(new Date().toISOString())
  const [usePost, setPost] = useState({ id })
  const [useUpdate, setUpdate] = useState(false)
  const { amount = 1, comment = '', additional = {}, selector = {} } = usePost
  const [useAmount, setAmount] = useState(amount)
  const [useComment, setComment] = useState(comment)
  const [useSelector, setSelector] = useState(JSON.stringify(selector))
  const [useAdditional, setAdditional] = useState(JSON.stringify(additional))

  const altAdd = () => {
    // NOTE: Eu realmente espero um valor boleano aqui
    // eslint-disable-next-line no-extra-boolean-cast
    if (!!useCart.find((data) => data.moment === useMoment)) {
      setUpdate(true)
      setCart((prevState) => {
        const assoc = [...prevState]
        assoc[assoc.map((val) => val.moment).indexOf(useMoment)] = {
          ...usePost,
          moment: useMoment
        }
        return assoc
      })
    } else {
      setUpdate(true)
      setCart((prevState) => {
        const assoc = [...prevState]
        assoc.push({
          ...usePost,
          moment: useMoment
        })
        return assoc
      })
    }
  }

  useEffect(() => {
    if (useStatus.current) {
      if (!useUpdate) {
        $(useSucessLayout.current).fadeOut(300)
      }
    } else {
      useStatus.current = true
    }
  }, [useUpdate])

  const altRemove = () => {
    const assoc = [...useCart]
    assoc.splice(assoc.map((val) => val.moment).indexOf(useMoment), 1)
    setCart(assoc)
    setPost({ id })
    setAmount(1)
    setComment('')
    setSelector(JSON.stringify({}))
    setAdditional(JSON.stringify({}))
  }

  const altComment = (val) =>
    setPost((prevState) => Object({ ...prevState, comment: val }))

  return (
    <>
      <ComponentOnCart
        {...{
          useUpdate,
          setUpdate,
          useStatus,
          useSucessLayout,
          useMoment,
          setMoment,
          usePost,
          setPost,
          setAmount,
          setComment,
          setSelector,
          setAdditional
        }}
      />
      <Content>
        <Thumb>
          {!!price_off && <OFF>{price_off}% OFF</OFF>}
          <Back type="button" onClick={() => history.goBack()}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              viewBox="0 0 451.111 451.111"
              fill="#fff"
            >
              <path d="m451.111 193.333h-322.222l112.778-112.777-48.333-48.333-193.334 193.333 193.333 193.333 48.333-48.333-112.777-112.778h322.222z" />
            </svg>
          </Back>
          {thumb.length ? (
            <ComponentAwaitProductThumb {...{ thumb }} />
          ) : (
            <ComponentProductThumb404 />
          )}
        </Thumb>
        <Sticky>
          <Title>
            <Name>{title}</Name>
            <Remove
              type="button"
              amountActive={!!useCart.find((data) => data.moment === useMoment)}
              onClick={() =>
                !!useCart.find((data) => data.moment === useMoment) &&
                altRemove()
              }
            >
              Remover
            </Remove>
          </Title>
          <More>
            <span>
              {weight
                ? Bracael.unitSize({ ml: weight, str: true })
                : Bracael.unitSize({ gm: grams, str: true })}
            </span>
            <Prices>
              <Price>
                {Number(price - (price * price_off) / 100).toLocaleString(
                  'pt-BR',
                  { style: 'currency', currency: 'BRL' }
                )}
              </Price>
              {!!price_off && (
                <Old>
                  {Number(price).toLocaleString('pt-BR', {
                    style: 'currency',
                    currency: 'BRL'
                  })}
                </Old>
              )}
            </Prices>
          </More>
          {!!description && <Description>{description}</Description>}
          {Object.keys(extra).length ? (
            <ComponentProductAdditional {...{ extra, usePost, setPost }} />
          ) : null}
          {Object.keys(pick).length
            ? Object.entries(pick).map((data, i) => (
                <ComponentProductPick
                  key={data[0]}
                  {...{ data, i, usePost, setPost }}
                />
              ))
            : null}
          <Comment>
            <h4>Alguma observação?</h4>
            <div>
              <Textarea
                maxLength="140"
                placeholder={
                  category.includes('Drinks') || category.includes('Dessert')
                    ? 'Escrever sua obserção aqui.'
                    : 'Ex: tirar a cebola, carne mal passada etc.'
                }
                onChange={(e) => altComment(e.target.value)}
                value={comment}
              />
            </div>
          </Comment>
        </Sticky>
      </Content>
      <Footer>
        <DFooter>
          <ComponentProductButtons {...{ usePost, setPost }} />
          <Comprar
            type="button"
            amountActive={Bracael.isValid({
              usePost,
              data,
              useAmount,
              useComment,
              useSelector,
              useAdditional,
              useMoment,
              useCart
            })}
            onClick={() =>
              Bracael.isValid({
                usePost,
                data,
                useAmount,
                useComment,
                useSelector,
                useAdditional,
                useMoment,
                useCart
              }) && altAdd()
            }
          >
            <span>
              {useCart.find((data) => data.moment === useMoment)
                ? 'Atualizar!'
                : 'Eu quero!'}
            </span>
            <span>
              {Number(
                amount * (price - (price * price_off) / 100) +
                  Object.entries(additional)
                    .map((assoc) =>
                      Number(assoc[1].amount * extra[assoc[0]].value)
                    )
                    .reduce((a, b) => a + b, 0)
              ).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
            </span>
          </Comprar>
        </DFooter>
      </Footer>
    </>
  )
}

export default ComponentProductId
