import React, { useEffect, useRef } from 'react'

import { SnapshotTarjeta } from '../contexts/Submit'

import ComponentTarjetaMain from './ComponentTarjetaMain'

import { Modal, Close, Content } from '../styles/Modal'

const { jQuery: $ } = window

function ComponentConfirmaTarjeta() {
  const modalRef = useRef(null)
  const statusonRef = useRef(false)
  const statusoffRef = useRef(false)
  const { useTarjeta, setTarjeta } = SnapshotTarjeta()

  useEffect(() => {
    const { status } = useTarjeta
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200)
    } else {
      statusonRef.current = true
    }
  }, [statusonRef, modalRef, useTarjeta])

  useEffect(() => {
    const { status } = useTarjeta
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setTarjeta({}))
    } else {
      statusoffRef.current = true
    }
  }, [useTarjeta])

  const cancenlFn = () => setTarjeta({ status: false })

  return (
    <Modal ref={modalRef}>
      {useTarjeta.status || useTarjeta.status === false ? (
        <>
          <Close onClick={() => cancenlFn()} />
          <Content>
            <ComponentTarjetaMain close={cancenlFn} />
          </Content>
        </>
      ) : null}
    </Modal>
  )
}

export default ComponentConfirmaTarjeta
