import React, { useEffect, useRef } from 'react'

import { SnapshotTrade } from '../contexts/Submit'

import ComponentTradeMain from './ComponentTradeMain'

import { Modal, Close, Content } from '../styles/Modal'

const { jQuery: $ } = window

function ComponentConfirmaTrade() {
  const modalRef = useRef(null)
  const statusonRef = useRef(false)
  const statusoffRef = useRef(false)
  const { useTrade, setTrade } = SnapshotTrade()

  useEffect(() => {
    const { status } = useTrade
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200)
    } else {
      statusonRef.current = true
    }
  }, [statusonRef, modalRef, useTrade])

  useEffect(() => {
    const { status } = useTrade
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setTrade({}))
    } else {
      statusoffRef.current = true
    }
  }, [useTrade])

  const cancenlFn = () => setTrade({ status: false })

  return (
    <Modal ref={modalRef}>
      {useTrade.status || useTrade.status === false ? (
        <>
          <Close onClick={() => cancenlFn()} />
          <Content>
            <ComponentTradeMain close={cancenlFn} />
          </Content>
        </>
      ) : null}
    </Modal>
  )
}

export default ComponentConfirmaTrade
