import React from 'react'

import Bracael from '../Bracael'
import ComponentMapCard from './ComponentMapCard'

import { Content, Small } from '../styles/Card'

function ComponentCards({ data, configGlobal }) {
  const { id_feed } = configGlobal

  return (
    <Content>
      {data.length ? (
        data.map((data) => <ComponentMapCard key={data[0]} {...{ data }} />)
      ) : (
        <Small>
          Não encontramos nada para{' '}
          {Bracael.category_names[id_feed].toLowerCase()}...
        </Small>
      )}
    </Content>
  )
}

export default ComponentCards
