import React, { useState } from 'react'

import { SnapshotPost } from '../contexts/Confirma'

import ComponentConfirmaCash from './ComponentConfirmaCash'
import ComponentConfirmaCard from './ComponentConfirmaCard'
import ComponentConfirmaPix from './ComponentConfirmaPix'

import { Contently, Checkradio } from '../styles/Payment'

function ComponentConfirmaPayment() {
  const { usePost, setPost } = SnapshotPost()
  const {
    cash = false,
    pix = false,
    credit = [],
    debit = [],
    voucher = []
  } = usePost.payment || {}
  const [useCard, setCard] = useState(
    !![...debit, ...credit, ...voucher].length
  )

  const altStateCash = () =>
    setPost((prevState) => {
      const { payment, trade = 0 } = prevState
      const { cash = false } = payment || {}

      return {
        ...prevState,
        payment: {
          ...payment,
          cash: !cash
        },
        trade: cash ? trade : 0
      }
    })

  const altStatePix = () =>
    setPost((prevState) => {
      const { payment } = prevState
      const { pix = false } = payment || {}

      return {
        ...prevState,
        payment: {
          ...payment,
          pix: !pix
        }
      }
    })

  return (
    <>
      <Contently>
        <Checkradio>
          <input type="checkbox" readOnly checked={cash} />
          <label onClick={() => altStateCash()}>Pagar com dinheiro?</label>
        </Checkradio>
        <ComponentConfirmaCash />
      </Contently>
      <Contently>
        <Checkradio>
          <input type="checkbox" readOnly checked={useCard} />
          <label onClick={() => setCard((prevState) => !prevState)}>
            Pagar no cartão?
          </label>
        </Checkradio>
        <ComponentConfirmaCard {...{ useCard, setCard }} />
      </Contently>
      <Contently>
        <Checkradio>
          <input type="checkbox" readOnly checked={pix} />
          <label onClick={() => altStatePix()}>Pagar com Pix?</label>
        </Checkradio>
        <ComponentConfirmaPix />
      </Contently>
    </>
  )
}

export default ComponentConfirmaPayment
