import React, { useRef, useState, useEffect } from 'react'
import { HashRouter } from 'react-router-dom'
import { DefaultLayout } from 'react-photoswipe-gallery'
import { ToastContainer, toast } from 'react-toastify'
import styled from 'styled-components'

import { database } from './services/server'
import ElementProvider from './contexts/App'

import ComponentMain from './components/ComponentMain'
import ComponentCart from './components/ComponentCart'
import ComponentSwipe from './components/ComponentSwipe'

import { Inset } from './styles/root'

// import 'keen-slider/keen-slider.min.css'

// import 'photoswipe/dist/photoswipe.css'
// import 'photoswipe/dist/default-skin/default-skin.css'

// import 'react-toastify/dist/ReactToastify.css'

function App(snapshot) {
  const { token, ...rest } = snapshot || {}

  const layoutRef = useRef()
  const statusRef = useRef(false)
  const tokenRef = useRef(token)

  const {
    profile = {},
    theme = {},
    mask = {},
    root = {},
    accessibility = {}
  } = rest || {}
  const businessRef = useRef({ profile, theme, mask, root, accessibility })
  const { status = false } = profile || {}

  const [useWhere, setWhere] = useState(
    document.querySelector('[name=local]').content
  )
  const [useSwipe, setSwipe] = useState(false)
  const [useBag, setBag] = useState(false)
  const [useStatus, setStatus] = useState(status)

  const [useService, setService] = useState(() => {
    const { itemService = JSON.stringify([]) } = localStorage
    try {
      return JSON.parse(itemService)
    } catch (err) {
      return []
    }
  })

  const [useCart, setCart] = useState(() => {
    const { itemCart = JSON.stringify([]) } = sessionStorage
    try {
      return JSON.parse(itemCart)
    } catch (err) {
      return []
    }
  })

  sessionStorage.setItem('itemCart', JSON.stringify(useCart))
  localStorage.setItem('itemService', JSON.stringify(useService))

  /*  :------------------------------------:
      | REALTIME DATABASE
      :------------------------------------:  */

  useEffect(() => {
    database()
      .ref(`${useWhere}/business/outside/profile/status`)
      .on('value', (snapshot) => {
        if (statusRef.current) {
          const options = {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined
          }

          if (snapshot.val()) {
            toast.success('Já estamos aberto!', options)
          } else {
            toast.error('Ei, estamos fechando...', options)
          }
          setStatus(snapshot.val())
        } else {
          statusRef.current = true
        }
      })
  }, [useWhere])

  return (
    <HashRouter>
      <ElementProvider
        {...{
          useSwipe,
          setSwipe,
          useBag,
          setBag,
          useCart,
          setCart,
          useWhere,
          setWhere,
          layoutRef,
          useStatus,
          setStatus,
          useService,
          setService,
          businessRef,
          tokenRef
        }}
      >
        <DefaultLayout
          shareButton={false}
          fullscreenButton={true}
          zoomButton={true}
          ref={layoutRef}
        />
        <Inset useActive={useBag}>
          <ComponentMain />
          <ComponentCart />
          <ComponentSwipe />
        </Inset>
        <StyledContainer />
      </ElementProvider>
    </HashRouter>
  )
}

export default App

