import React from 'react'
import { useAsync } from 'react-async'

import { getImageUrl } from '../app/'

import { SnapshotWhere } from '../contexts/App'
import { storage } from '../services/server'

import ComponentProductThumb from './ComponentProductThumb'
import ComponentSkeletonLoadThumb from './ComponentSkeletonLoadThumb'
import ComponentProductThumb404 from './ComponentProductThumb404'

const defaultGravatar =
  'data:image/png;base64,'

async function processFn(val) {
  const img = new Image()
  img.src = val
  await img.decode()
  return {
    width: String(img.width),
    height: String(img.height),
    src: img.src
  }
}

const awaitStorage = async (data, where) => {
  async function onResolve(foundURL) {
    return await processFn(foundURL)
  }

  // ANCHOR verificar o status de erro no front-end
  function onReject() {
    console.clear()
    return { error: true }
  }

  return await Promise.all(
    data.map(async (data) => {
      const { name } = data

      return {
        thumbnail: await storage()
          .ref(getImageUrl({ where, ref: `${name}_640x640.jpeg` }))
          .getDownloadURL()
          .then(onResolve, onReject)
          .catch(() => defaultGravatar),
        original: await storage()
          .ref(getImageUrl({ where, ref: `${name}_1024x1024.jpeg` }))
          .getDownloadURL()
          .then(onResolve, onReject)
          .catch(() => defaultGravatar)
      }
    })
  )
}

const fetchPerson = async ({ useWhere, thumb }) => {
  return await awaitStorage(thumb, useWhere).then((data) =>
    data.filter((data) => !data.original.error && !data.thumbnail.error)
  )
}

function ComponentAwaitProductThumb({ thumb }) {
  const { useWhere } = SnapshotWhere()
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, thumb })

  if (error) return error.message
  if (data)
    return data.length ? (
      <ComponentProductThumb {...{ data }} />
    ) : (
      <ComponentProductThumb404 />
    )
  return <ComponentSkeletonLoadThumb />
}

export default ComponentAwaitProductThumb
