import React from 'react'

import { SnapshotStatus } from '../contexts/App'

import { Content, Title, Status } from '../styles/Headsub'

function ComponentHeadsub({ configGlobal }) {
  const { name_content } = configGlobal
  const { useStatus } = SnapshotStatus()

  return (
    <Content>
      <Title>{name_content}</Title>
      <Status status={useStatus}>
        {useStatus ? 'Delivery aberto!' : 'Delivery fechado!'}
      </Status>
    </Content>
  )
}

export default ComponentHeadsub
