import React, { useRef, useState } from 'react'

import ElementProvider from '../contexts/Service'

import ComponentModalClient from './ComponentModalClient'
import ComponentModalComanda from './ComponentModalComanda'
import ComponentModalUpService from './ComponentModalUpService'
import ComponentMapService from './ComponentMapService'

import { Content } from '../styles/Service'
import { Small } from '../styles/Service'

function ComponentService({ data, useUpdate, setUpdate }) {
  const { order, cupons, feed, delivery } = data
  const dataRef = useRef({ order, cupons, feed, delivery })
  const [useOrder, setOrder] = useState(order)
  const [useClient, setClient] = useState({})
  const [useComanda, setComanda] = useState({})

  return (
    <ElementProvider
      {...{
        useClient,
        setClient,
        useComanda,
        setComanda,
        useOrder,
        setOrder,
        dataRef
      }}
    >
      <Content>
        {useOrder.filter((data) => data.error !== true).length ? (
          useOrder
            .filter((data) => data.error !== true)
            .map((data, i) => (
              <ComponentMapService
                key={data}
                {...{ data, i, feed, delivery }}
              />
            ))
        ) : (
          <Small style={{ minHeight: '150px' }}>
            Seus últimos pedidos foram removidos.
          </Small>
        )}
      </Content>
      <ComponentModalClient />
      <ComponentModalComanda />
      {useOrder.map((data) => !!data.error).includes(true) ? (
        <ComponentModalUpService {...{ useUpdate, setUpdate }} />
      ) : null}
    </ElementProvider>
  )
}

export default ComponentService
