import React from 'react'

import ComponentMapBairros from './ComponentMapBairros'

import { Content, Small } from '../styles/Bairros'

function ComponentBairros({ data }) {
  return (
    <Content>
      {Object.keys(data).length ? (
        Object.entries(data)
          .sort((a, b) =>
            a[1].district > b[1].district
              ? 1
              : b[1].district > a[1].district
              ? -1
              : 0
          )
          .map((data, i, all) => (
            <ComponentMapBairros key={data[0]} {...{ data, i, all }} />
          ))
      ) : (
        <Small>Não há nenhum bairro publicado no momento.</Small>
      )}
    </Content>
  )
}

export default ComponentBairros
