import React from 'react'

import { Item } from 'react-photoswipe-gallery'

import { Image, Outthumb } from '../styles/Product'

function ComponentMapProductThumb({ data, i }) {
  const { original, thumbnail } = data

  const settings = {
    original: original.src,
    thumbnail: thumbnail.src,
    width: original.width,
    height: original.height,
    id: i
  }

  return (
    <Item {...settings}>
      {({ ref, open }) => (
        <Image className="keen-slider__slide">
          <Outthumb style={{ backgroundImage: 'url(' + thumbnail.src + ')' }} />
          <img
            ref={ref}
            src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEAAAEALAAAAAABAAEAgM7t3M7t3AICTAEAOw=="
            height="100%"
            width="100%"
            alt=""
            style={{ backgroundImage: 'url(' + thumbnail.src + ')' }}
            onClick={open}
          />
        </Image>
      )}
    </Item>
  )
}

export default ComponentMapProductThumb
