import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router'

import { Close } from '../styles/Modal'
import {
  Modal,
  Content,
  Title,
  Main,
  Description,
  Footer,
  Filled,
  Outline,
  Text
} from '../styles/OnCart'

const { jQuery: $ } = window

function ComponentOnCart({
  useUpdate,
  setUpdate,
  useStatus,
  useSucessLayout,
  setMoment,
  usePost,
  setPost,
  setAmount,
  setComment,
  setSelector,
  setAdditional
}) {
  const history = useHistory()
  const [useRoute, setRoute] = useState(false)
  const {
    id,
    amount = 1,
    comment = '',
    additional = {},
    selector = {}
  } = usePost

  useEffect(() => {
    if (useStatus.current && useUpdate) {
      $(useSucessLayout.current).fadeIn(200)
    }
  }, [useStatus, useSucessLayout, useUpdate])

  useEffect(() => {
    if (useRoute) {
      history.push('/confirma')
    }
  }, [useRoute])

  const newFn = () => {
    setUpdate(false)
    setMoment(new Date().toISOString())
    setPost({ id })
    setAmount(1)
    setComment('')
    setSelector(JSON.stringify({}))
    setAdditional(JSON.stringify({}))
  }

  const followFn = () => {
    setUpdate(false)
    setAmount(amount)
    setComment(comment)
    setSelector(JSON.stringify(selector))
    setAdditional(JSON.stringify(additional))
  }

  const finishFn = () => setRoute(true)

  return (
    <Modal ref={useSucessLayout}>
      <Close onClick={() => followFn()} />
      <Content>
        <Main>
          <Title>Já esta na sacola!</Title>
          <Description>Você quer continuar e adicionar algo mais?</Description>
          <Footer>
            <Filled type="button" onClick={() => newFn()}>
              <Text>Continuar</Text>
            </Filled>
            <Outline type="button" onClick={() => finishFn()}>
              <Text>Finalizar</Text>
            </Outline>
          </Footer>
        </Main>
      </Content>
    </Modal>
  )
}

export default ComponentOnCart
