import React, { useEffect, useRef } from 'react'

import { SnapshotPost } from '../contexts/Confirma'
import { SnapshotTrade } from '../contexts/Submit'

import { Content, Main, Value, To } from '../styles/Payment'

const { jQuery: $ } = window

function ComponentConfirmaCash() {
  const statusRef = useRef(false)
  const methodRef = useRef(null)
  const { setTrade } = SnapshotTrade()
  const { usePost, setPost } = SnapshotPost()
  const { trade = 0, payment } = usePost
  const { cash = false } = payment || {}

  const tradeFn = () => setTrade({ status: true })

  useEffect(() => {
    if (statusRef.current || cash) {
      $(methodRef.current).animate({ height: 'toggle' }, 300, function () {
        if (!cash) {
          setPost((prevState) => {
            // eslint-disable-next-line no-unused-vars
            const { payment, trade = 0, ...rest } = prevState
            // eslint-disable-next-line no-unused-vars
            const { cash = false, ...rest_payment } = payment || {}

            return {
              ...rest,
              payment: {
                ...rest_payment
              }
            }
          })
        }
      })
      statusRef.current = true
    } else {
      statusRef.current = true
    }
  }, [cash])

  return (
    <Content ref={methodRef}>
      <Main>
        <Value>
          {Number(trade).toLocaleString('pt-BR', {
            style: 'currency',
            currency: 'BRL'
          })}
        </Value>
        <To type="button" onClick={() => tradeFn()}>
          Troco?
        </To>
      </Main>
    </Content>
  )
}

export default ComponentConfirmaCash
