import React from 'react'

import { SnapshotScreen } from '../contexts/Confirma'

import ComponentConfirmaClient from './ComponentConfirmaClient'
import ComponentComanda from './ComponentComanda'
import ComponentConfirmSubmit from './ComponentConfirmSubmit'

function ComponentConfirm({ data }) {
  const { useScreen } = SnapshotScreen()

  if (useScreen === 1) return <ComponentConfirmaClient {...{ data }} />
  else if (useScreen === 2) return <ComponentComanda {...{ data }} />
  else if (useScreen === 3) return <ComponentConfirmSubmit {...{ data }} />
  return <div>Algo saiu mal.</div>
}

export default ComponentConfirm
