import React, { useState } from 'react'

import { SnapshotService } from '../contexts/App'

import ComponentHead from '../components/ComponentHead'
import ComponentAwaitService from '../components/ComponentAwaitService'
import ComponentNav from '../components/ComponentNav'

import { Scroll } from '../styles/root'
import { Small } from '../styles/Service'

function Entregas() {
  const { useService } = SnapshotService()
  const [useUpdate, setUpdate] = useState(1)

  return (
    <>
      <ComponentHead />
      <Scroll style={{ backgroundColor: '#f6f7f9', zIndex: 'initial' }}>
        {useService.length ? (
          useUpdate & 1 ? (
            <ComponentAwaitService key={1} {...{ useUpdate, setUpdate }} />
          ) : (
            <ComponentAwaitService key={2} {...{ useUpdate, setUpdate }} />
          )
        ) : (
          <Small>Por enquanto, não há nada por aqui.</Small>
        )}
      </Scroll>
      <ComponentNav />
    </>
  )
}

export default Entregas
