import React, { useEffect } from 'react'
import { useAsync } from 'react-async'
import { toast } from 'react-toastify'

import Bracael from '../Bracael'
import { functions } from '../services/server'
import { SnapshotToken } from '../contexts/App'
import { SnapshotCupom, SnapshotPost } from '../contexts/Confirma'
import { SnapshotStatus } from '../contexts/Cupom'

import ComponentSkeletonCupom from './ComponentSkeletonCupom'

const fetchPerson = async ({ token, cupom_promotion }) =>
  await functions()
    .httpsCallable('queryCupom')({ token, code: cupom_promotion })
    .then((data) => data.data)

function ComponentAwaitCupom() {
  const { token } = SnapshotToken()
  const { setCupom } = SnapshotCupom()
  const { usePost, setPost } = SnapshotPost()
  const { setStatus } = SnapshotStatus()
  const { cupom_promotion } = usePost
  const { data, error } = useAsync({
    promiseFn: fetchPerson,
    token,
    cupom_promotion
  })

  useEffect(() => {
    if (data) {
      const { status, ...rest } = data
      if (status === 200) {
        setStatus((prevState) =>
          Object({
            ...prevState,
            status: true
          })
        )
        setCupom((prevState) =>
          Object({
            ...prevState,
            ...rest
          })
        )
        setPost((prevState) =>
          Object({
            ...prevState,
            cupom_promotion
          })
        )
      } else {
        toast.error('Código inválido!', Bracael.toast_options)
        const assoc = { ...usePost }
        delete assoc.cupom_promotion
        setPost(assoc)
      }
    }
  }, [data])

  useEffect(() => {
    if (error) {
      const assoc = { ...usePost }
      delete assoc.cupom_promotion
      setPost(assoc)

      if (window.navigator.onLine) {
        toast.error('Erro ao buscar, tente novamente', Bracael.toast_options)
      } else {
        toast.error(
          'Não foi possível conectar-se à internet',
          Bracael.toast_options
        )
      }
    }
  }, [error])

  return <ComponentSkeletonCupom />
}

export default ComponentAwaitCupom
