import React from 'react'

import { Li, Input, Label, Subname } from '../styles/Pick'

function ComponentMapPick({ item, data, id, usePost, setPost }) {
  const { subname } = item
  // eslint-disable-next-line no-unused-vars
  const { name, limit = 1, important, list } = data[1]

  const { selector = {} } = usePost

  const bag = selector[id] || []

  const CambioInput = () => {
    if (bag.length < limit) {
      if (bag.includes(subname)) {
        const assoc = { ...usePost }
        assoc.selector[id].splice(assoc.selector[id].indexOf(subname), 1)
        setPost(assoc)
      } else {
        setPost((prevState) => {
          const assoc = { ...prevState }
          assoc.selector = {
            ...selector,
            [id]: [...bag, subname]
          }
          return assoc
        })
      }
    } else {
      if (bag.includes(subname)) {
        const assoc = { ...usePost }
        assoc.selector[id].splice(assoc.selector[id].indexOf(subname), 1)
        setPost(assoc)
      }
    }
  }

  return (
    <Li>
      <Input type="checkbox" readOnly checked={bag.includes(subname)} />
      <Label onClick={() => CambioInput()}>
        <Subname>{subname}</Subname>
      </Label>
    </Li>
  )
}

export default ComponentMapPick
