import React, { createContext, useContext } from 'react'

const ElementContext = createContext()

export default function ElementProvider({
  children,
  useSwipe,
  setSwipe,
  useBag,
  setBag,
  useCart,
  setCart,
  useWhere,
  setWhere,
  layoutRef,
  useStatus,
  setStatus,
  useService,
  setService,
  businessRef,
  tokenRef
}) {
  return (
    <ElementContext.Provider
      value={{
        useSwipe,
        setSwipe,
        useBag,
        setBag,
        useCart,
        setCart,
        useWhere,
        setWhere,
        layoutRef,
        useStatus,
        setStatus,
        useService,
        setService,
        businessRef,
        tokenRef
      }}
    >
      {children}
    </ElementContext.Provider>
  )
}

export function SnapshotSwipe() {
  const context = useContext(ElementContext)
  const { useSwipe, setSwipe } = context
  return { useSwipe, setSwipe }
}

export function SnapshotBag() {
  const context = useContext(ElementContext)
  const { useBag, setBag } = context
  return { useBag, setBag }
}

export function SnapshotCart() {
  const context = useContext(ElementContext)
  const { useCart, setCart } = context
  return { useCart, setCart }
}

export function SnapshotWhere() {
  const context = useContext(ElementContext)
  const { useWhere, setWhere } = context
  return { useWhere, setWhere }
}

export function SnapshotPhotoswipe() {
  const context = useContext(ElementContext)
  const { layoutRef } = context
  return { layoutRef }
}

export function SnapshotStatus() {
  const context = useContext(ElementContext)
  const { useStatus, setStatus } = context
  return { useStatus, setStatus }
}

export function SnapshotService() {
  const context = useContext(ElementContext)
  const { useService, setService } = context
  return { useService, setService }
}

export function SnapshotBusiness() {
  const context = useContext(ElementContext)
  const { businessRef } = context
  return { ...businessRef.current }
}

export function SnapshotToken() {
  const context = useContext(ElementContext)
  const { tokenRef } = context
  return { token: tokenRef.current }
}
