import React, { useState } from 'react'

import Bracael from '../Bracael'

import ComponentMenuItens from './ComponentMenuItens'

import { Content } from '../styles/Menu'

function ComponentMenu({ data, screenRef, swipeRef, useMenuId, setMenuId }) {
  const [useSwipeable, setSwipeable] = useState({
    isDown: false
  })

  // eslint-disable-next-line no-unused-vars
  const { isDown, startX, scrollLeft, active } = useSwipeable

  const MouseDown = (event) => {
    setSwipeable((prevState) =>
      Object({
        ...prevState,
        isDown: true,
        active: true,
        scrollLeft: swipeRef.current.scrollLeft,
        startX: event.pageX - swipeRef.current.offsetLeft
      })
    )

    if (!active) {
      swipeRef.current.classList.add('active')
    }
  }

  const MouseLeave = () => {
    setSwipeable((prevState) =>
      Object({
        ...prevState,
        active: false,
        isDown: false
      })
    )

    if (active) {
      swipeRef.current.classList.remove('active')
    }
  }

  const MouseUp = () => {
    setSwipeable((prevState) =>
      Object({
        ...prevState,
        active: false,
        isDown: false
      })
    )

    if (active) {
      swipeRef.current.classList.remove('active')
    }
  }

  const MouseMove = (event) => {
    const x = event.pageX - swipeRef.current.offsetLeft
    const walk = (x - startX) * 1.25

    event.preventDefault()

    if (!isDown) return // para a function

    if (isDown) {
      swipeRef.current.scrollLeft = scrollLeft - walk
    }
  }

  return (
    <Content
      draggable="true"
      ref={swipeRef}
      onMouseDown={MouseDown}
      onMouseLeave={MouseLeave}
      onMouseUp={MouseUp}
      onMouseMove={MouseMove}
    >
      {Object.keys(Bracael.category_names)
        .filter((id) => {
          return !!Object.entries(data)
            .filter((data) =>
              id === '005'
                ? data[1].category === id &&
                  (data[1].available || {})[Bracael.u.d[new Date().getDay()]]
                : data[1].category === id
            )
            .filter(
              (data) =>
                (data[1].status || 200) === 200 ||
                (data[1].status || 200) === 403
            ).length
        })
        .map((id) => (
          <ComponentMenuItens
            key={id}
            {...{ id, screenRef, useMenuId, setMenuId }}
          />
        ))}
    </Content>
  )
}

export default ComponentMenu
