import React from 'react'
import { useKeenSlider } from 'keen-slider/react'

import { CustomGallery } from 'react-photoswipe-gallery'
import PhotoswipeUIDefault from 'photoswipe/dist/photoswipe-ui-default'

import { SnapshotPhotoswipe } from '../contexts/App'

import ComponentMapProductThumb from './ComponentMapProductThumb'

function ComponentProductThumb({ data }) {
  const { layoutRef } = SnapshotPhotoswipe()
  const [sliderRef] = useKeenSlider()

  return (
    <CustomGallery ui={PhotoswipeUIDefault} layoutRef={layoutRef} id="photo">
      <div ref={sliderRef} className="keen-slider">
        {data.map((data, i) => (
          <ComponentMapProductThumb key={i} {...{ data, i }} />
        ))}
      </div>
    </CustomGallery>
  )
}

export default ComponentProductThumb
