import React from 'react'

import { Li, Hash, Name, QTD, Price } from '../styles/Comanda'

function ComponentComandaExtra({ assoc, extra }) {
  const uid = assoc[0]
  const { amount } = assoc[1]
  const { name, value } = extra[uid]

  return (
    <Li>
      <Hash>--</Hash>
      <Name>Extra de {name}</Name>
      <QTD>{amount}un</QTD>
      <Price>
        {Number(amount * value).toLocaleString('pt-BR', {
          style: 'currency',
          currency: 'BRL'
        })}
      </Price>
    </Li>
  )
}

export default ComponentComandaExtra
