import React, { useState, useEffect, useRef } from 'react'

import { SnapshotCart, SnapshotStatus } from '../contexts/App'
import ElementProvider from '../contexts/Confirma'

import ComponentHead from '../components/ComponentHead'
import ComponentAwaitConfirm from '../components/ComponentAwaitConfirm'
import ComponentOverload from '../components/ComponentOverload'
import ComponentConfirmaSucess from '../components/ComponentConfirmaSucess'
import ComponentConfirmaStart from '../components/ComponentConfirmaStart'
import ComponentNav from '../components/ComponentNav'

import { Small } from '../styles/Confirm'

const { jQuery: $ } = window

function Confirma() {
  const cartRef = useRef(false)
  const statusRef = useRef(false)
  const modalRef = useRef(null)
  const { useCart } = SnapshotCart()
  const { useStatus } = SnapshotStatus()
  const [usePost, setPost] = useState({ detail: useCart })
  const [useStart, setStart] = useState({})
  const [useScreen, setScreen] = useState(1)
  const [useSection, setSection] = useState(1)
  const [useSucess, setSucess] = useState({ status: false })
  const [useCupom, setCupom] = useState({})
  const { status, id } = useSucess
  const [useModal, setModal] = useState({})
  const { order_type } = usePost

  useEffect(() => {
    const { status } = useModal
    if (statusRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setModal({}))
    } else {
      statusRef.current = true
    }
  }, [useModal])

  useEffect(() => {
    if (cartRef.current) {
      setPost((prevState) =>
        Object({
          ...prevState,
          detail: useCart
        })
      )
    } else {
      cartRef.current = true
    }
  }, [useCart])

  return status && id ? (
    <ComponentConfirmaSucess {...{ useSucess, setSucess, usePost, setPost }} />
  ) : (
    <>
      <ComponentHead />
      <ElementProvider
        {...{
          useStart,
          setStart,
          usePost,
          setPost,
          useSection,
          setSection,
          useScreen,
          setScreen,
          useSucess,
          setSucess,
          useModal,
          setModal,
          useCupom,
          setCupom
        }}
      >
        {useCart.length && useStatus ? (
          order_type ? (
            <ComponentAwaitConfirm />
          ) : (
            <ComponentConfirmaStart />
          )
        ) : useStatus ? (
          <Small>Seu carinho está vazio.</Small>
        ) : (
          <Small>
            Estamos fechado no momento, volte quando o delivery estiver em
            aberto.
          </Small>
        )}
        <ComponentOverload {...{ useModal, modalRef }} />
      </ElementProvider>
      <ComponentNav />
    </>
  )
}

export default Confirma
