import React, { createContext, useContext } from 'react'

const ElementContext = createContext()

export default function ElementProvider({
  children,
  useClient,
  setClient,
  useComanda,
  setComanda,
  useOrder,
  setOrder,
  dataRef
}) {
  return (
    <ElementContext.Provider
      value={{
        useClient,
        setClient,
        useComanda,
        setComanda,
        useOrder,
        setOrder,
        dataRef
      }}
    >
      {children}
    </ElementContext.Provider>
  )
}

export function SnapshotClient() {
  const context = useContext(ElementContext)
  const { useClient, setClient } = context
  return { useClient, setClient }
}

export function SnapshotComanda() {
  const context = useContext(ElementContext)
  const { useComanda, setComanda } = context
  return { useComanda, setComanda }
}

export function SnapshotOrder() {
  const context = useContext(ElementContext)
  const { useOrder, setOrder } = context
  return { useOrder, setOrder }
}

export function SnapshotData() {
  const context = useContext(ElementContext)
  const { dataRef } = context
  const { order, cupons, feed, delivery } = dataRef.current
  return { order, cupons, feed, delivery }
}
