import React from 'react'

import { Li, Input, Label } from '../styles/Tarjeta'

function ComponentMethodCredit({ item, useMethods, setMethods }) {
  const [id] = item
  const { credit } = JSON.parse(useMethods)

  const altMethod = () => {
    if (credit.includes(id)) {
      const assoc = { ...JSON.parse(useMethods) }
      assoc.credit.splice(assoc.credit.indexOf(id), 1)
      setMethods(JSON.stringify(assoc))
    } else {
      const assoc = { ...JSON.parse(useMethods) }
      assoc.credit.push(id)
      setMethods(JSON.stringify(assoc))
    }
  }

  return (
    <Li>
      <Input type="checkbox" readOnly checked={credit.includes(id)} />
      <Label onClick={() => altMethod()}>{id}</Label>
    </Li>
  )
}

export default ComponentMethodCredit
