import React from 'react'

import Bracael from '../Bracael'
import { SnapshotBusiness } from '../contexts/App'
import { SnapshotData, SnapshotOrder } from '../contexts/Service'

import ComponentMapMComanda from './ComponentMapMComanda'

import { Container } from '../styles/MComanda'
import {
  Content,
  Head,
  Line,
  Border,
  Strong,
  Hash,
  Name,
  QTD,
  Price,
  List,
  Subtotal,
  Frete,
  Total
} from '../styles/Comanda'

function ComponentComandaMain({ id }) {
  const { profile } = SnapshotBusiness()
  const {
    brand,
    cnpj,
    store_street,
    store_district,
    store_number,
    address_ref,
    store_zone,
    store_uf
  } = profile
  const { delivery, cupons, feed } = SnapshotData()
  const { useOrder } = SnapshotOrder()
  const { detail, delivery_select, order_type, cupom_promotion } =
    useOrder.find((data) => data.id === id)
  const { type_off = '001', value = 0 } = cupons[cupom_promotion] || {}

  const { rate = 0 } = delivery[delivery_select] || {}

  const totalCart =
    type_off === '001'
      ? parseFloat(
          (
            Bracael.subtotal({ detail, feed }) -
            (Bracael.subtotal({ detail, feed }) * value) / 100
          ).toFixed(2)
        )
      : parseFloat((Bracael.subtotal({ detail, feed }) + ~value + 1).toFixed(2))

  return (
    <Container>
      <Head>
        <Line>
          <b>{brand} Hamburgueria</b>
        </Line>
        <Line>CNPJ {cnpj}</Line>
        <Line>
          {store_street}, {store_number}{' '}
          {address_ref ? `- ${address_ref} -` : '-'} {store_district} -{' '}
          {store_zone} - {store_uf}
        </Line>
      </Head>
      <Content>
        <Border>
          Esta nota não substitui o cupom fiscal, portanto, é uma forma fácil de
          que o cliente está acostumado a visualizar os detalhes no pedido.
        </Border>
        <Strong>
          <Hash>#</Hash>
          <Name>DESCRIÇÃO</Name>
          <QTD>QTD.</QTD>
          <Price>TOTAL</Price>
        </Strong>
        <List>
          {detail.map((assoc, i) => (
            <ComponentMapMComanda key={assoc.moment} {...{ assoc, i }} />
          ))}
        </List>
        <Subtotal>
          <span>SUBTOTAL</span>
          <span>
            {Number(Bracael.subtotal({ detail, feed })).toLocaleString(
              'pt-BR',
              { style: 'currency', currency: 'BRL' }
            )}
          </span>
        </Subtotal>
        {order_type !== 2 && (
          <Frete>
            <span>
              <small>ACRESCIMO</small>VALOR DA ENTREGA
            </span>
            <span>
              {Number(rate).toLocaleString('pt-BR', {
                style: 'currency',
                currency: 'BRL'
              })}
            </span>
          </Frete>
        )}
        {!value && !!cupom_promotion ? (
          <Frete>
            <span>
              <small>DESCONTO</small>CUPOM INDEFINIDO
            </span>
            <span>Oferta não encontrada</span>
          </Frete>
        ) : (
          !!value && (
            <Frete>
              <span>
                <small>DESCONTO</small>CUPOM {value}% OFF
              </span>
              <span>
                {type_off !== '001'
                  ? Number(~value + 1).toLocaleString('pt-BR', {
                      style: 'currency',
                      currency: 'BRL'
                    })
                  : Number(
                      -Math.abs(Bracael.subtotal({ detail, feed }) - totalCart)
                    ).toLocaleString('pt-BR', {
                      style: 'currency',
                      currency: 'BRL'
                    })}
              </span>
            </Frete>
          )
        )}
        <Total>
          <b>TOTAL A PAGAR</b>
          <span>
            {Number(totalCart + rate).toLocaleString('pt-BR', {
              style: 'currency',
              currency: 'BRL'
            })}
          </span>
        </Total>
      </Content>
    </Container>
  )
}

export default ComponentComandaMain
