import React, { useEffect, useRef, useState } from 'react'

import { SnapshotPost } from '../contexts/Confirma'

import {
  Main,
  Head,
  Title,
  Form,
  Input,
  Label,
  Footer,
  Button
} from '../styles/Trade'

const { jQuery: $ } = window

function ComponentTradeMain({ close }) {
  const inputRef = useRef(null)
  const { usePost, setPost } = SnapshotPost()
  const { trade = 0 } = usePost
  const [useInput, setInput] = useState(trade ? trade.toFixed(2) : '')

  const altInput = (val) => setInput(val)

  useEffect(() => {
    inputRef.current.value = useInput
  }, [useInput])

  useEffect(() => {
    $(inputRef.current).mask('#.##0,00', { reverse: true })
  }, [inputRef])

  const saveFn = () => {
    close()
    setPost((prevState) =>
      Object({
        ...prevState,
        trade: Number(
          inputRef.current
            ? inputRef.current.value.replace(/\./g, '').replace(/,/g, '.')
            : 0
        )
      })
    )
  }

  const isValid = () =>
    isNaN(Number(useInput))
      ? Number(useInput.replace(/\./g, '').replace(/,/g, '.'))
      : Number(useInput) !== trade

  return (
    <Main>
      <Head>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          height="18"
          width="18"
          viewBox="0 0 515.556 515.556"
        >
          <path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm32.222 386.667h-64.444v-64.444h64.444zm.002-96.667h-64.444v-161.111h64.444z" />
        </svg>
        <Title>Quantia em mãos</Title>
      </Head>
      <Form>
        <Input
          type="text"
          ref={inputRef}
          placeholder="0,00"
          status={true}
          onInput={(e) => altInput(e.target.value)}
          autoCapitalize="off"
          autoCorrect="off"
          onPaste={(e) => e.preventDefault()}
          onCopy={(e) => e.preventDefault()}
          onCut={(e) => e.preventDefault()}
          onDrag={(e) => e.preventDefault()}
          onDrop={(e) => e.preventDefault()}
          autoComplete="off"
        />
        <Label>R$</Label>
      </Form>
      <Footer>
        <Button
          type="button"
          status={isValid()}
          onClick={() => isValid() && saveFn()}
        >
          Salvar
        </Button>
      </Footer>
    </Main>
  )
}

export default ComponentTradeMain
