import React from 'react'

import Bracael from '../Bracael'
import { SnapshotBusiness } from '../contexts/App'
import {
  SnapshotScreen,
  SnapshotPost,
  SnapshotCupom
} from '../contexts/Confirma'

import ComponentMapComanda from './ComponentMapComanda'

import {
  Container,
  Content,
  Head,
  Main,
  Line,
  Border,
  Strong,
  Hash,
  Name,
  QTD,
  Price,
  List,
  Subtotal,
  Frete,
  Total,
  Footer,
  Simple,
  Button
} from '../styles/Comanda'

function ComponentComanda({ data }) {
  const { feed, delivery } = data
  const { profile } = SnapshotBusiness()
  const {
    brand,
    cnpj,
    store_street,
    store_district,
    store_number,
    address_ref,
    store_zone,
    store_uf
  } = profile
  const { setScreen } = SnapshotScreen()
  const { useCupom } = SnapshotCupom()
  const { type_off = '001', value = 0 } = useCupom

  const { usePost } = SnapshotPost()
  const { detail, delivery_select, order_type } = usePost

  const { rate = 0 } = delivery[delivery_select] || {}

  const totalCart =
    type_off === '001'
      ? parseFloat(
          (
            Bracael.subtotal({ detail, feed }) -
            (Bracael.subtotal({ detail, feed }) * value) / 100
          ).toFixed(2)
        )
      : parseFloat((Bracael.subtotal({ detail, feed }) + ~value + 1).toFixed(2))

  return (
    <Container>
      <Main>
        <Head>
          <Line>
            <b>{brand} Hamburgueria</b>
          </Line>
          <Line>CNPJ {cnpj}</Line>
          <Line>
            {store_street}, {store_number}{' '}
            {address_ref ? `- ${address_ref} -` : '-'} {store_district} -{' '}
            {store_zone} - {store_uf}
          </Line>
        </Head>
        <Content>
          <Border>
            Esta nota não substitui o cupom fiscal, portanto, é uma forma fácil
            de que o cliente está acostumado a visualizar os detalhes no pedido.
          </Border>
          <Strong>
            <Hash>#</Hash>
            <Name>DESCRIÇÃO</Name>
            <QTD>QTD.</QTD>
            <Price>TOTAL</Price>
          </Strong>
          <List>
            {detail.map((data, i) => (
              <ComponentMapComanda key={data.moment} {...{ data, i, feed }} />
            ))}
          </List>
          <Subtotal>
            <span>SUBTOTAL</span>
            <span>
              {Number(Bracael.subtotal({ detail, feed })).toLocaleString(
                'pt-BR',
                { style: 'currency', currency: 'BRL' }
              )}
            </span>
          </Subtotal>
          {order_type !== 2 && (
            <Frete>
              <span>
                <small>ACRESCIMO</small>VALOR DA ENTREGA
              </span>
              <span>
                {Number(rate).toLocaleString('pt-BR', {
                  style: 'currency',
                  currency: 'BRL'
                })}
              </span>
            </Frete>
          )}
          {!!value && (
            <Frete>
              <span>
                <small>DESCONTO</small>CUPOM {value}% OFF
              </span>
              <span>
                {type_off !== '001'
                  ? Number(~value + 1).toLocaleString('pt-BR', {
                      style: 'currency',
                      currency: 'BRL'
                    })
                  : Number(
                      -Math.abs(Bracael.subtotal({ detail, feed }) - totalCart)
                    ).toLocaleString('pt-BR', {
                      style: 'currency',
                      currency: 'BRL'
                    })}
              </span>
            </Frete>
          )}
          <Total>
            <b>TOTAL A PAGAR</b>
            <span>
              {Number(totalCart + rate).toLocaleString('pt-BR', {
                style: 'currency',
                currency: 'BRL'
              })}
            </span>
          </Total>
        </Content>
      </Main>
      <Footer>
        <Simple type="button" onClick={() => setScreen(1)}>
          Voltar atrás
        </Simple>
        <Button type="button" onClick={() => setScreen(3)}>
          Próximo!
        </Button>
      </Footer>
    </Container>
  )
}

export default ComponentComanda
