import React, { useEffect, useRef } from 'react'
import { toast } from 'react-toastify'

import {
  SnapshotPost,
  SnapshotScreen,
  SnapshotSection
} from '../contexts/Confirma'

import {
  Content,
  Main,
  Group,
  Flex,
  Input,
  Label,
  Column,
  INFO,
  Bottom,
  Text,
  Help,
  Button
} from '../styles/Confirm'

const { jQuery: $ } = window

function ComponentConfirmPersonal() {
  const telephoneRef = useRef(null)

  const { setScreen } = SnapshotScreen()
  const { setSection } = SnapshotSection()
  const { usePost, setPost } = SnapshotPost()
  const { display_name = '', telephone_number = '', order_type } = usePost

  const altInputNumber = (val) =>
    setPost((prevState) =>
      Object({
        ...prevState,
        display_name: val
      })
    )

  const altInputTelephone = (val) =>
    setPost((prevState) =>
      Object({
        ...prevState,
        telephone_number: val
      })
    )

  useEffect(() => {
    var SPMaskBehavior = function (val) {
        return val.replace(/\D/g, '').length === 11
          ? '(00) 00000-0000'
          : '(00) 0000-00009'
      },
      spOptions = {
        onKeyPress: function (val, e, field, options) {
          field.mask(SPMaskBehavior.apply({}, arguments), options)
        }
      }

    const { current } = telephoneRef
    $(current).mask(SPMaskBehavior, spOptions)
  }, [telephoneRef])

  const isValidStatus = () => {
    return (
      display_name.trim().replace(/\s+/g, ' ').split(' ').length >= 2 &&
      telephone_number.replace(/[^0-9]/g, '').length >= 10
    )
  }

  const isValid = () => {
    const isFalse = () => {
      toast.warn('Nome ou telefone inválido!', {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      })
      return false
    }

    return isValidStatus() ? true : isFalse()
  }

  const confirmFn = () => {
    if (isValid()) {
      if (order_type === 2) {
        setScreen(2)
      } else {
        setSection(2)
      }
    }
  }

  return (
    <Content>
      <Main>
        <Group>
          <Flex>
            <Input
              type="text"
              status={display_name}
              onInput={(e) => altInputNumber(e.target.value)}
              autoCapitalize="on"
              autoCorrect="off"
              onPaste={(e) => e.preventDefault()}
              onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()}
              onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()}
              autoComplete="off"
              value={display_name}
            />
            <Label>Nome e sobrenome</Label>
          </Flex>
        </Group>
        <Group>
          <Flex>
            <Input
              type="text"
              ref={telephoneRef}
              status={true}
              placeholder="Ex.: (11) 0000-0000"
              onInput={(e) => altInputTelephone(e.target.value)}
              autoCapitalize="on"
              autoCorrect="off"
              onPaste={(e) => e.preventDefault()}
              onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()}
              onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()}
              autoComplete="off"
              value={telephone_number}
            />
            <Label>Telefone</Label>
          </Flex>
        </Group>
      </Main>
      <Column>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          viewBox="0 0 16 16"
          fill="#ea1d2c"
        >
          <path
            d="M7,3H9v7H7V3Zm0,8H9v2H7V11ZM8,0A8,8,0,1,1,0,8,8,8,0,0,1,8,0Z"
            fillRule="evenodd"
          ></path>
        </svg>
        <INFO>
          O telefone é importante para casos de dificuldade de encontrar o
          endereço.
        </INFO>
      </Column>
      <Bottom>
        <Text>
          Ao continuar, você concorda
          <br />
          com os{' '}
          <Help
            type="button"
            onClick={() => alert('Página em desenvolvimento.')}
          >
            Termos de Compra
          </Help>
        </Text>
        <Button
          type="button"
          status={isValidStatus()}
          onClick={() => confirmFn()}
        >
          Próximo passo
        </Button>
      </Bottom>
    </Content>
  )
}

export default ComponentConfirmPersonal
