import React from 'react'
import { useAsync } from 'react-async'

import { functions } from '../services/server'
import { SnapshotToken, SnapshotCart } from '../contexts/App'

import ComponentConfirm from './ComponentConfirm'
import ComponentSkeletonConfirm from './ComponentSkeletonConfirm'

const filterValues = ({ val, data }) => {
  const unique = (value, index, self) => {
    return self.indexOf(value) === index
  }

  return data.map((assoc) => assoc[val]).filter(unique)
}

const fetchPerson = async ({ token, useCart }) =>
  Object({
    feed: await functions()
      .httpsCallable('listProducts')({
        token,
        data: filterValues({ val: 'id', data: useCart })
      })
      .then((data) => data.data)
      .then((data) =>
        data.reduce((out, { id, ...rest }) => {
          return Object({ ...out, [id]: { ...rest } })
        }, {})
      ),
    delivery: await functions()
      .httpsCallable('queryObject')({ token, ref: 'business/delivery' })
      .then((data) => data.data)
  })

function ComponentAwaitConfirm() {
  const { token } = SnapshotToken()
  const { useCart } = SnapshotCart()
  const { data, error } = useAsync({ promiseFn: fetchPerson, token, useCart })

  if (error) return error.message
  if (data) return <ComponentConfirm {...{ data }} />
  return <ComponentSkeletonConfirm />
}

export default ComponentAwaitConfirm
