import React from 'react'

import Bracael from '../Bracael'

import ComponentMapHome from './ComponentMapHome'

import ComponentMenu from '../components/ComponentMenu'

import { Container } from '../styles/Home'

function ComponentHome({ data, screenRef, swipeRef, useMenuId, setMenuId }) {
  return (
    <>
      <ComponentMenu {...{ data, screenRef, swipeRef, useMenuId, setMenuId }} />
      <Container>
        {Object.keys(Bracael.category_names)
          .filter((id) => {
            return !!Object.entries(data)
              .filter((data) =>
                id === '005'
                  ? data[1].category === id &&
                    (data[1].available || {})[Bracael.u.d[new Date().getDay()]]
                  : data[1].category === id
              )
              .filter(
                (data) =>
                  (data[1].status || 200) === 200 ||
                  (data[1].status || 200) === 403
              ).length
          })
          .map((id) => (
            <ComponentMapHome key={id} {...{ id, data }} />
          ))}
      </Container>
    </>
  )
}

export default ComponentHome
