import React from 'react'

import { SnapshotStatus, SnapshotBusiness } from '../contexts/App'

import { Content, MAX, Marca, Status } from '../styles/Head'

function ComponentHead() {
  const { useStatus } = SnapshotStatus()
  const { mask } = SnapshotBusiness()
  const { brand_header } = mask

  return (
    <Content>
      <MAX>
        <Marca url={brand_header} />
      </MAX>
      <Status status={useStatus}>
        {useStatus ? 'Delivery aberto!' : 'Delivery fechado!'}
      </Status>
    </Content>
  )
}

export default ComponentHead
