import React from 'react'

import { Li, Input, Label } from '../styles/Tarjeta'

function ComponentMethodDebit({ item, useMethods, setMethods }) {
  const [id] = item
  const { debit } = JSON.parse(useMethods)

  const altMethod = () => {
    if (debit.includes(id)) {
      const assoc = { ...JSON.parse(useMethods) }
      assoc.debit.splice(assoc.debit.indexOf(id), 1)
      setMethods(JSON.stringify(assoc))
    } else {
      const assoc = { ...JSON.parse(useMethods) }
      assoc.debit.push(id)
      setMethods(JSON.stringify(assoc))
    }
  }

  return (
    <Li>
      <Input type="checkbox" readOnly checked={debit.includes(id)} />
      <Label onClick={() => altMethod()}>{id}</Label>
    </Li>
  )
}

export default ComponentMethodDebit
