import React, { useEffect, useRef, useState } from 'react'
import { toast } from 'react-toastify'

import { SnapshotSection, SnapshotPost } from '../contexts/Confirma'

import ComponentMapDelivery from './ComponentMapDelivery'

import {
  Content,
  Main,
  Group,
  Flex,
  Input,
  Select,
  SLT,
  Bottom,
  Simple,
  Button,
  Empty
} from '../styles/Confirm'

function ComponentConfirmDelivery({ data }) {
  const statusRef = useRef(false)
  const { delivery = {} } = data

  const { usePost } = SnapshotPost()
  const { delivery_select } = usePost
  const { setSection } = SnapshotSection()
  const [useInput, setInput] = useState('')
  const [useDelivery, setDelivery] = useState(delivery)

  useEffect(() => {
    if (statusRef.current) {
      setDelivery(
        Object.fromEntries(
          Object.entries(delivery).filter(
            (item) =>
              item[1].district.toLowerCase().indexOf(useInput.toLowerCase()) !==
              -1
          )
        )
      )
    } else {
      statusRef.current = true
    }
  }, [data, delivery, useInput])

  const isValidStatus = () => {
    return delivery_select
  }

  const isValid = () => {
    const isFalse = () => {
      toast.warn('Selecione o seu bairro...', {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      })
      return false
    }

    return isValidStatus() ? true : isFalse()
  }

  return (
    <Content>
      <Main>
        <Group>
          <Flex>
            <Input
              type="text"
              placeholder="Buscar bairro..."
              onInput={(e) => setInput(e.target.value)}
              autoCapitalize="on"
              autoCorrect="off"
              onPaste={(e) => e.preventDefault()}
              onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()}
              onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()}
              autoComplete="off"
              value={useInput}
            />
          </Flex>
        </Group>
        <Select>
          {Object.keys(useDelivery).length ? (
            <SLT>
              {Object.entries(useDelivery)
                .sort((a, b) =>
                  a[1].district > b[1].district
                    ? 1
                    : b[1].district > a[1].district
                    ? -1
                    : 0
                )
                .map((data, i, all) => (
                  <ComponentMapDelivery key={data[0]} {...{ data, i, all }} />
                ))}
            </SLT>
          ) : (
            <Empty>Não há resultados</Empty>
          )}
        </Select>
      </Main>
      <Bottom>
        <Simple type="button" onClick={() => setSection(1)}>
          Voltar atrás
        </Simple>
        <Button
          type="button"
          status={isValidStatus()}
          onClick={() => isValid() && setSection(3)}
        >
          Próximo passo
        </Button>
      </Bottom>
    </Content>
  )
}

export default ComponentConfirmDelivery
