import React from 'react'

import { SnapshotCart } from '../contexts/App'

import { Icon } from '../styles/Nav'

function ComponentIconCart() {
  const { useCart } = SnapshotCart()

  return useCart.length ? (
    <Icon>
      {useCart
        .map((data) => {
          const { amount = 1 } = data
          return amount
        })
        .reduce((a, b) => a + b, 0)}
    </Icon>
  ) : null
}

export default ComponentIconCart
