import React from 'react'

import Bracael from '../Bracael'
import { SnapshotData } from '../contexts/Service'

import ComponentComandaSelector from './ComponentComandaSelector'
import ComponentComandaExtra from './ComponentComandaExtra'

import { Li, Hash, Name, QTD, Price } from '../styles/Comanda'

function ComponentMapMComanda({ assoc, i }) {
  const { feed } = SnapshotData()

  const { id, amount = 1, additional = {}, selector = {}, comment } = assoc
  const {
    title,
    price,
    weight,
    grams,
    extra = {},
    pick = {},
    price_off = 0
  } = feed[id]

  return (
    <>
      <Li>
        <Hash>{new Array(3 + 1 - (i + '').length).join('0') + ++i}</Hash>
        <Name>
          {title}{' '}
          {weight
            ? Bracael.unitSize({ ml: weight })
            : grams
            ? Bracael.unitSize({ gm: grams })
            : ''}
        </Name>
        <QTD>{amount}un</QTD>
        <Price>
          {Number(amount * (price - (price * price_off) / 100)).toLocaleString(
            'pt-BR',
            { style: 'currency', currency: 'BRL' }
          )}
        </Price>
      </Li>
      {Object.keys(selector).length
        ? Object.entries(selector).map((assoc) => (
            <ComponentComandaSelector key={assoc[0]} {...{ assoc, pick }} />
          ))
        : null}
      {Object.keys(additional).length
        ? Object.entries(additional).map((assoc) => (
            <ComponentComandaExtra key={assoc[0]} {...{ assoc, extra }} />
          ))
        : null}
      {comment ? (
        <Li color="#0300cc">
          <Hash>--</Hash>
          <Name>{comment}</Name>
          <QTD>--</QTD>
          <Price>--</Price>
        </Li>
      ) : null}
    </>
  )
}

export default ComponentMapMComanda
