import React, { createContext, useContext } from 'react'

const ElementContext = createContext()

export default function ElementProvider({
  children,
  useStart,
  setStart,
  usePost,
  setPost,
  useSection,
  setSection,
  useScreen,
  setScreen,
  useSucess,
  setSucess,
  useModal,
  setModal,
  useCupom,
  setCupom
}) {
  return (
    <ElementContext.Provider
      value={{
        useStart,
        setStart,
        usePost,
        setPost,
        useSection,
        setSection,
        useScreen,
        setScreen,
        useSucess,
        setSucess,
        useModal,
        setModal,
        useCupom,
        setCupom
      }}
    >
      {children}
    </ElementContext.Provider>
  )
}

export function SnapshotCupom() {
  const context = useContext(ElementContext)
  const { useCupom, setCupom } = context
  return { useCupom, setCupom }
}

export function SnapshotStart() {
  const context = useContext(ElementContext)
  const { useStart, setStart } = context
  return { useStart, setStart }
}

export function SnapshotPost() {
  const context = useContext(ElementContext)
  const { usePost, setPost } = context
  return { usePost, setPost }
}

export function SnapshotSection() {
  const context = useContext(ElementContext)
  const { useSection, setSection } = context
  return { useSection, setSection }
}

export function SnapshotScreen() {
  const context = useContext(ElementContext)
  const { useScreen, setScreen } = context
  return { useScreen, setScreen }
}

export function SnapshotSucess() {
  const context = useContext(ElementContext)
  const { useSucess, setSucess } = context
  return { useSucess, setSucess }
}

export function SnapshotModal() {
  const context = useContext(ElementContext)
  const { useModal, setModal } = context
  return { useModal, setModal }
}
