import React from 'react'
import { useHistory } from 'react-router'

import ComponentAwaitCardThumb from './ComponentAwaitCardThumb'
import ComponentProductThumbNull from './ComponentProductThumbNull'

import {
  Li,
  Sub,
  Description,
  Value,
  Price,
  Button,
  Unavailable,
  OFF,
  Old
} from '../styles/Card'

function ComponentMapCard({ data }) {
  const history = useHistory()

  const [id, assoc] = data
  // eslint-disable-next-line no-unused-vars
  const {
    status = 200,
    description,
    price,
    thumb = [],
    title,
    price_off = 0
  } = assoc

  return (
    <Li>
      <Sub>
        <h4>{title}</h4>
        {description ? <Description>{description}</Description> : null}
        <Value>
          <Price>
            {Number(price - (price * price_off) / 100).toLocaleString('pt-BR', {
              style: 'currency',
              currency: 'BRL'
            })}
          </Price>
          {!!price_off && (
            <Old>
              {Number(price).toLocaleString('pt-BR', {
                style: 'currency',
                currency: 'BRL'
              })}
            </Old>
          )}
          {status === 403 && <Unavailable>Indisponível</Unavailable>}
          {!!price_off && <OFF>{price_off}% OFF</OFF>}
        </Value>
      </Sub>
      {thumb.length ? (
        <ComponentAwaitCardThumb {...{ thumb }} />
      ) : (
        <ComponentProductThumbNull />
      )}
      {status !== 403 ? (
        <Button type="button" onClick={() => history.push(`/p/${id}`)}></Button>
      ) : null}
    </Li>
  )
}

export default ComponentMapCard
