import React from 'react'

import { SnapshotPost } from '../contexts/Confirma'

import { Li, L, R, A, Name, Price, Checked } from '../styles/Confirm'

function ComponentMapDelivery({ data, i, all }) {
  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost()
  const { delivery_select } = usePost
  // eslint-disable-next-line no-unused-vars
  const { district, rate, zone = '' } = data[1]

  const letter = all[i - 1]
    ? district.substring(0, 1) !== all[i - 1][1].district.substring(0, 1)
      ? all[i][1].district.substring(0, 1)
      : ''
    : all[i][1].district.substring(0, 1)

  const altSelect = () =>
    setPost((prevState) =>
      Object({
        ...prevState,
        delivery_select: data[0]
      })
    )

  return (
    <Li>
      <L>
        <A>{letter}</A>
        <Name>
          {district}
          {zone ? ` (${zone})` : ''}
        </Name>
      </L>
      <R>
        <Price>
          {rate
            ? Number(rate).toLocaleString('pt-BR', {
                style: 'currency',
                currency: 'BRL'
              })
            : 'GRÁTIS'}
        </Price>
        <Checked type="radio" readOnly checked={delivery_select === data[0]} />
        <div />
      </R>
      <label onClick={() => altSelect()}></label>
    </Li>
  )
}

export default ComponentMapDelivery
