import React, { useEffect, useRef, useState } from 'react'

import { SnapshotService } from '../contexts/App'
import { SnapshotOrder } from '../contexts/Service'

import { Modal, Content } from '../styles/Modal'
import {
  Main,
  Title,
  Description,
  Footer,
  Button,
  Text
} from '../styles/MService'

const { jQuery: $ } = window

function ComponentModalUpService({ setUpdate }) {
  const modalRef = useRef(null)
  const statusoffRef = useRef(false)
  const { useService, setService } = SnapshotService()
  const { useOrder } = SnapshotOrder()
  const [useModal, setModal] = useState({ status: true })

  useEffect(() => {
    $(modalRef.current).fadeIn(200)
  }, [])

  useEffect(() => {
    const { status } = useModal
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => {
        const assoc = [...useService].filter(
          (val) => useOrder.find((data) => data.id === val).error !== true
        )
        setService(assoc)
        setUpdate((prevState) => prevState + 1)
        // estranhamente, quando o estado `setUpdate` esta acima de `setService` ele tem um comportamento estranho ao atualizar o os cards, os cards é atualizados mais ainda continua os antigos.
      })
    } else {
      statusoffRef.current = true
    }
  }, [useModal])

  const cancenlFn = () => setModal({ status: false })

  return (
    <Modal ref={modalRef} zIndex="6">
      <Content zIndex="4" alignItems="center">
        <Main>
          <Title>Existe pedidos antigos...</Title>
          <Description>
            Um ou outro pedido antigo foi encontrado, vamos limpar esta lista!
          </Description>
          <Footer>
            <Button type="button" onClick={() => cancenlFn()}>
              <Text>Continuar</Text>
            </Button>
          </Footer>
        </Main>
      </Content>
    </Modal>
  )
}

export default ComponentModalUpService
