import React from 'react'
import { useAsync } from 'react-async'

import { functions } from '../services/server'
import { SnapshotToken } from '../contexts/App'

import ComponentHome from './ComponentHome'
import ComponentSkeletonHome from './ComponentSkeletonHome'

const fetchPerson = async ({ token }) =>
  await functions()
    .httpsCallable('queryObject')({ token, ref: 'feed' })
    .then((data) => data.data)
    .then((data) =>
      Object.fromEntries(
        Object.entries(data).filter(
          (data) =>
            (data[1].status || 200) === 200 || (data[1].status || 200) === 403
        )
      )
    )

function ComponentAwaitHome({
  configGlobal,
  screenRef,
  swipeRef,
  useMenuId,
  setMenuId
}) {
  const { token } = SnapshotToken()
  const { data, error } = useAsync({ promiseFn: fetchPerson, token })

  if (error) return error.message
  if (data)
    return (
      <ComponentHome
        {...{ data, configGlobal, screenRef, swipeRef, useMenuId, setMenuId }}
      />
    )
  return <ComponentSkeletonHome {...{ swipeRef }} />
}

export default ComponentAwaitHome
