import React from 'react'

import { Li, L, R, B, Name, Price } from '../styles/Bairros'

function ComponentMapBairros({ data, i, all }) {
  const { district, rate, zone = '' } = data[1]

  const letter = all[i - 1]
    ? district.substring(0, 1) !== all[i - 1][1].district.substring(0, 1)
      ? all[i][1].district.substring(0, 1)
      : ''
    : all[i][1].district.substring(0, 1)

  return (
    <Li>
      <L>
        <B>{letter}</B>
        <Name>
          {district}
          {zone ? ` (${zone})` : ''}
        </Name>
      </L>
      <R>
        <Price value={rate}>
          {rate
            ? Number(rate).toLocaleString('pt-BR', {
                style: 'currency',
                currency: 'BRL'
              })
            : 'GRÁTIS'}
        </Price>
      </R>
    </Li>
  )
}

export default ComponentMapBairros
