import React from 'react'

import { Li, Input, Label } from '../styles/Tarjeta'

function ComponentMethodVoucher({ item, useMethods, setMethods }) {
  const [id] = item
  const { voucher } = JSON.parse(useMethods)

  const altMethod = () => {
    if (voucher.includes(id)) {
      const assoc = { ...JSON.parse(useMethods) }
      assoc.voucher.splice(assoc.voucher.indexOf(id), 1)
      setMethods(JSON.stringify(assoc))
    } else {
      const assoc = { ...JSON.parse(useMethods) }
      assoc.voucher.push(id)
      setMethods(JSON.stringify(assoc))
    }
  }

  return (
    <Li>
      <Input type="checkbox" readOnly checked={voucher.includes(id)} />
      <Label onClick={() => altMethod()}>{id}</Label>
    </Li>
  )
}

export default ComponentMethodVoucher
