import React from 'react'
import { useHistory } from 'react-router-dom'

import {
  Content,
  Head,
  Main,
  Column,
  Title,
  Text,
  MSG,
  Footer,
  Button,
  OU,
  Url
} from '../styles/Sucess'

function ComponentConfirmaSucess({ useSucess, usePost }) {
  const history = useHistory()
  // eslint-disable-next-line no-unused-vars
  const { status, id, url } = useSucess
  const { display_name = '' } = usePost

  const altWindowOpen = () => {
    window.open(url)
  }

  return (
    <Content>
      <Head>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="72"
          height="72"
          viewBox="0 0 515.556 515.556"
          fill="#2AEA6E"
        >
          <path d="m0 274.226 176.549 176.886 339.007-338.672-48.67-47.997-290.337 290-128.553-128.552z" />
        </svg>
      </Head>
      <Main>
        <Column>
          <Title>Olá {display_name.split(' ')[0]},</Title>
          <Text>Seu pedido foi enviado!</Text>
        </Column>
        <Column>
          <MSG>
            Obrigado pelo seu pedido! <strong>E SE VOCÊ</strong> não conseguiu
            enviar para nós a comanda via WhatsApp, tente através do botão
            abaixo.
          </MSG>
        </Column>
        <Footer>
          <Button type="button" onClick={() => altWindowOpen()}>
            Enviar via WhatsApp
          </Button>
          <OU>
            <span>ou</span>
          </OU>
          <Url type="button" onClick={() => history.push('/')}>
            Ir para a página inicial
          </Url>
        </Footer>
      </Main>
    </Content>
  )
}

export default ComponentConfirmaSucess
