import React from 'react'
import { useHistory } from 'react-router'

import Bracael from '../Bracael'

import ComponentMapCard from './ComponentMapCard'

import { Content, Head, Title, Main, Footer, SEE } from '../styles/Home'

function ComponentMapHome({ id, data }) {
  const history = useHistory()

  const organize = (a, b) =>
    a[1].date_create < b[1].date_create
      ? 1
      : b[1].date_create < a[1].date_create
      ? -1
      : 0

  return (
    <Content data-section={id}>
      <Head>
        <Title>{Bracael.category_names[id]}</Title>
      </Head>
      <Main>
        {Object.entries(data)
          .filter((data) => data[1].category === id)
          .sort(organize)
          .filter((data, i) => i < 5)
          .map((data) => (
            <ComponentMapCard key={data[0]} {...{ data }} />
          ))}
      </Main>
      {Object.entries(data).filter((data) => data[1].category === id).length >
      5 ? (
        <Footer>
          <SEE
            type="button"
            onClick={() => history.push(Bracael.category_routers[id])}
          >
            <span>{Bracael.category_names[id]}</span>
            <small>Mostrar tudo sobre</small>
          </SEE>
        </Footer>
      ) : null}
    </Content>
  )
}

export default ComponentMapHome
