import React from 'react'

import { SnapshotPost, SnapshotSection } from '../contexts/Confirma'

import Bracael from '../Bracael'

import {
  Content,
  Contently,
  Head,
  Headly,
  Sup,
  Sub,
  Button
} from '../styles/Resume'

function ComponentConfirmaResume() {
  const { usePost, setPost } = SnapshotPost()
  const { setSection } = SnapshotSection()
  const { order_type } = usePost

  const handleCancel = () => {
    setPost((prevState) => {
      // NOTE: Talvez o assoc não seja a melhor opção aqui
      const assoc = { ...prevState }
      delete assoc.order_type
      delete assoc.delivery_select
      delete assoc.house_number
      delete assoc.street
      delete assoc.complement

      return {
        ...assoc
      }
    })
    setSection(1)
  }

  return (
    <Content>
      <Contently>
        <Head>
          <Headly>
            <Sup>Tipo do pedido</Sup>
            <Sub>{Bracael.type_order[order_type].name}</Sub>
          </Headly>
          <Headly>
            <Button type="button" onClick={() => handleCancel()}>
              Alterar
            </Button>
          </Headly>
        </Head>
      </Contently>
    </Content>
  )
}

export default ComponentConfirmaResume
