import React from 'react'
import ReactDOM from 'react-dom'

import { functions } from './services/server'

import App from './App'

// import './index.css'

async function startDatabase() {
  return await functions()
    .httpsCallable('App')({
      where: document.querySelector('[name=local]').content
    })
    .then((data) => data.data)
}

const StartConnection = () => Promise.resolve(startDatabase())

StartConnection()
  // .then(function (data) {
  //   const { main_url = 'delivery.bracael.com' } = data.root || {};

  //   const { hostname, protocol } = new URL(window.location.href);

  //   // hostname === 'delivery.bracael.com' && protocol === 'https:' // validar para produção !IMPORTANT
  //   // console.log(hostname === 'localhost' && protocol === 'http:')

  //   return hostname === main_url
  //   && protocol === 'https:'
  //   && data !== undefined
  //     ? Promise.resolve(data)
  //     : Promise.reject()
  // })
  .then(function (data) {
    const { status, ...rest } = data

    if (status === 200)
      ReactDOM.render(<App {...rest} />, document.getElementById('root'))
    else ReactDOM.render(<div>404</div>, document.getElementById('root'))
  })
  .catch(function () {
    ReactDOM.render(<div>ERRO 500</div>, document.getElementById('root'))
  })
