import React from 'react'
import { toast } from 'react-toastify'

import {
  SnapshotPost,
  SnapshotSection,
  SnapshotScreen
} from '../contexts/Confirma'

import {
  Content,
  Main,
  Group,
  Flex,
  Input,
  Label,
  Bottom,
  Simple,
  Button
} from '../styles/Confirm'

function ComponentConfirmAddress() {
  const { usePost, setPost } = SnapshotPost()
  // eslint-disable-next-line no-unused-vars
  const { useSection, setSection } = SnapshotSection()
  // eslint-disable-next-line no-unused-vars
  const { useScreen, setScreen } = SnapshotScreen()
  const { street = '', house_number = '', complement = '' } = usePost

  const altInputStreet = (val) =>
    setPost((prevState) =>
      Object({
        ...prevState,
        street: val
      })
    )

  const altInputHouseNumber = (val) => {
    if (!isNaN(Number(val)) && val.replace(/[^0-9]/g, '') !== house_number) {
      setPost((prevState) =>
        Object({
          ...prevState,
          house_number: val
        })
      )
    }
  }

  const altInputOptional = (val) =>
    setPost((prevState) =>
      Object({
        ...prevState,
        complement: val
      })
    )

  const isValidStatus = () => {
    return street && house_number
  }

  const isValid = () => {
    const isFalse = () => {
      toast.warn('Preencha todos os campos...', {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      })
      return false
    }

    return isValidStatus() ? true : isFalse()
  }

  return (
    <Content>
      <Main>
        <Group>
          <Flex>
            <Input
              type="text"
              status={street}
              onInput={(e) => altInputStreet(e.target.value)}
              autoCapitalize="on"
              autoCorrect="off"
              onPaste={(e) => e.preventDefault()}
              onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()}
              onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()}
              autoComplete="off"
              value={street}
            />
            <Label>Rua</Label>
          </Flex>
        </Group>
        <Group>
          <Flex>
            <Input
              type="text"
              status={house_number}
              onInput={(e) => altInputHouseNumber(e.target.value)}
              autoCapitalize="on"
              autoCorrect="off"
              onPaste={(e) => e.preventDefault()}
              onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()}
              onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()}
              autoComplete="off"
              value={house_number}
            />
            <Label>Número</Label>
          </Flex>
          <Flex minWidth="68%">
            <Input
              type="text"
              status={true}
              placeholder="Opcional"
              onInput={(e) => altInputOptional(e.target.value)}
              autoCapitalize="on"
              autoCorrect="off"
              onPaste={(e) => e.preventDefault()}
              onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()}
              onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()}
              autoComplete="off"
              value={complement}
            />
            <Label>Complemento e referência</Label>
          </Flex>
        </Group>
      </Main>
      <Bottom>
        <Simple type="button" onClick={() => setSection(2)}>
          Voltar atrás
        </Simple>
        <Button
          type="button"
          status={isValidStatus()}
          onClick={() => isValid() && setScreen(2)}
        >
          Finalizar pedido
        </Button>
      </Bottom>
    </Content>
  )
}

export default ComponentConfirmAddress
