import React from 'react'

import { SnapshotSection, SnapshotPost } from '../contexts/Confirma'

import ComponentConfirmPersonal from './ComponentConfirmPersonal'
import ComponentConfirmDelivery from './ComponentConfirmDelivery'
import ComponentConfirmAddress from './ComponentConfirmAddress'

import {
  Content,
  Screen,
  Btnstep,
  Step,
  Name,
  Form,
  Table
} from '../styles/Form'

function ComponentConfirmaForm({ data }) {
  const { useSection } = SnapshotSection()
  const { usePost } = SnapshotPost()
  const { order_type } = usePost

  return (
    <Content>
      <Screen>
        <i />
        <Btnstep type="button" status={useSection === 1}>
          <Step>1</Step>
          <Name>Pessoal</Name>
        </Btnstep>
        <Btnstep
          type="button"
          status={useSection === 2}
          cursorDefault={order_type === 2}
        >
          <Step>2</Step>
          <Name>{order_type !== 2 ? 'Bairro' : '--'}</Name>
        </Btnstep>
        <Btnstep
          type="button"
          status={useSection === 3}
          cursorDefault={order_type === 2}
        >
          <Step>3</Step>
          <Name>{order_type !== 2 ? 'Endereço' : '--'}</Name>
        </Btnstep>
      </Screen>
      <Form showChild={useSection}>
        <Table>
          <ComponentConfirmPersonal />
        </Table>
        <Table>
          <ComponentConfirmDelivery {...{ data }} />
        </Table>
        <Table>
          <ComponentConfirmAddress />
        </Table>
      </Form>
    </Content>
  )
}

export default ComponentConfirmaForm
