import React, { useEffect, useRef } from 'react'
import { toast } from 'react-toastify'

import { SnapshotBusiness } from '../contexts/App'
import { SnapshotPost } from '../contexts/Confirma'

import { Content, Main, Text, To } from '../styles/Payment'

const { jQuery: $ } = window

function ComponentConfirmaPix() {
  const statusRef = useRef(false)
  const methodRef = useRef(null)
  const textareaRef = useRef(null)
  // eslint-disable-next-line no-unused-vars
  const { profile } = SnapshotBusiness()
  const { pix_username } = profile
  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost()
  const { payment } = usePost
  // eslint-disable-next-line no-unused-vars
  const { pix = false } = payment || {}

  const altCopy = () => {
    const { current } = textareaRef
    current.focus()
    current.select()

    try {
      document.execCommand('copy')
      toast.info('Copiado com sucesso!', {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      })
    } catch (err) {
      toast.error('Ops, incapaz de copiar!', {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      })
    }
  }

  useEffect(() => {
    const { current } = methodRef
    if (statusRef.current || pix) {
      $(current).animate({ height: 'toggle' }, 300)
      statusRef.current = true
    } else {
      statusRef.current = true
    }
  }, [pix])

  return (
    <Content ref={methodRef}>
      <Main>
        <Text ref={textareaRef} readOnly value={pix_username} />
        <To type="button" onClick={() => altCopy()}>
          Copiar
        </To>
      </Main>
    </Content>
  )
}

export default ComponentConfirmaPix
