import React, { useEffect, useRef, useState } from 'react'

import { SnapshotBag } from '../contexts/App'

import { Overlay } from '../styles/Overlay'

const { jQuery: $ } = window

function ComponentOverlay() {
  // eslint-disable-next-line no-unused-vars
  const { useBag, setBag } = SnapshotBag()
  const useComponentSwipe = useRef(null)
  const [menuOpenAndClose, setOpenAndClose] = useState(false)

  useEffect(() => {
    const { current } = useComponentSwipe
    const CloseMenuLatel = () => $(current).fadeIn(300)

    menuOpenAndClose && CloseMenuLatel()
  }, [menuOpenAndClose])

  useEffect(() => {
    const { current } = useComponentSwipe
    const CloseMenuLatel = () =>
      $(current).fadeOut(300, () => setOpenAndClose(false))

    useBag ? setOpenAndClose(true) : CloseMenuLatel()
  }, [useBag])

  return (
    menuOpenAndClose && (
      <Overlay ref={useComponentSwipe} onClick={() => setBag(false)} />
    )
  )
}

export default ComponentOverlay
