import React, { useState } from 'react'

import { SnapshotPost } from '../contexts/Confirma'
import ElementProvider from '../contexts/Cupom'

import ComponentAwaitCupom from './ComponentAwaitCupom'
import ComponentCupomInsert from './ComponentCupomInsert'
import ComponentCupomThen from './ComponentCupomThen'

function ComponentConfirmaCupom() {
  const [useStatus, setStatus] = useState({})
  const { usePost } = SnapshotPost()
  const { cupom_promotion } = usePost
  const { status = false } = useStatus

  return (
    <ElementProvider {...{ useStatus, setStatus }}>
      {cupom_promotion ? (
        status ? (
          <ComponentCupomThen />
        ) : (
          <ComponentAwaitCupom />
        )
      ) : (
        <ComponentCupomInsert />
      )}
    </ElementProvider>
  )
}

export default ComponentConfirmaCupom
