import React, { createContext, useContext } from 'react'

const ElementContext = createContext()

export default function ElementProvider({ children, useStatus, setStatus }) {
  return (
    <ElementContext.Provider value={{ useStatus, setStatus }}>
      {children}
    </ElementContext.Provider>
  )
}

export function SnapshotStatus() {
  const context = useContext(ElementContext)
  const { useStatus, setStatus } = context
  return { useStatus, setStatus }
}
