import React from 'react'

import ComponentMapCard from './ComponentMapCard'

import { Content } from '../styles/Card'

function ComponentDestaque({ data }) {
  const organize = (a, b) =>
    (a[1].purchase || 0) < (b[1].purchase || 0)
      ? 1
      : (b[1].purchase || 0) < (a[1].purchase || 0)
      ? -1
      : 0

  return (
    <Content>
      {Object.entries(data)
        .filter((data) => data[1].status === 200 || data[1].status === 403)
        .sort(organize)
        .filter((data, i) => i < 10)
        .map((data) => (
          <ComponentMapCard key={data[0]} {...{ data }} />
        ))}
    </Content>
  )
}

export default ComponentDestaque
