import React from 'react'

import Bracael from '../Bracael'
import { SnapshotBusiness } from '../contexts/App'

import { Li, Name, Clock } from '../styles/Semana'

function ComponentMapSemana({ data, i }) {
  const { profile } = SnapshotBusiness()
  const { streaming } = profile

  return (streaming[data] || [['', '']]).map((assoc, ii) => {
    const [a, b] = assoc

    return (
      <Li key={ii}>
        <Name>{ii ? '--' : Bracael.u.b[i]}</Name>
        {a && b ? (
          <Clock>
            {a} às {b}
          </Clock>
        ) : (
          <Clock style={{ color: '#ff0000' }}>Fechado</Clock>
        )}
      </Li>
    )
  })
}

export default ComponentMapSemana
