import React from 'react'
import { useAsync } from 'react-async'

import { functions } from '../services/server'
import { SnapshotToken, SnapshotService } from '../contexts/App'

import ComponentService from './ComponentService'
import ComponentSkeletonService from './ComponentSkeletonService'

const fetchPerson = async ({ token, useService }) => {
  return {
    order: await functions()
      .httpsCallable('queryService')({ token, data: useService })
      .then((data) => data.data),
    feed: await functions()
      .httpsCallable('queryObject')({ token, ref: 'feed' })
      .then((data) => data.data),
    delivery: await functions()
      .httpsCallable('queryObject')({ token, ref: 'business/delivery' })
      .then((data) => data.data),
    cupons: await functions()
      .httpsCallable('queryObject')({ token, ref: 'business/cupons' })
      .then((data) => data.data)
  }
}

function ComponentAwaitService({ useUpdate, setUpdate }) {
  const { useService } = SnapshotService()
  const { token } = SnapshotToken()
  const { data, error } = useAsync({
    promiseFn: fetchPerson,
    token,
    useService
  })

  if (error) return error.message
  if (data) return <ComponentService {...{ data, useUpdate, setUpdate }} />
  return <ComponentSkeletonService />
}

export default ComponentAwaitService
