import React from 'react'
import { useAsync } from 'react-async'

import { getImageUrl } from '../app/index'

import { storage } from '../services/server'
import { SnapshotWhere } from '../contexts/App'

import ComponentMapCardThumb from './ComponentMapCardThumb'
import ComponentSkeletonCardThumb from './ComponentSkeletonCardThumb'
import ComponentProductThumbNull from './ComponentProductThumbNull'

const awaitStorage = async (data, where) => {
  const { name } = data

  async function processFn(src) {
    const img = new Image()
    img.src = src
    await img.decode()
    return img.src
  }

  return await storage()
    .ref(getImageUrl({ where, ref: `${name}_320x320.jpeg` }))
    .getDownloadURL()
    .then(processFn)
}

const fetchPerson = async ({ thumb, useWhere }) => {
  const [image] = thumb
  return await awaitStorage(image, useWhere)
}

function ComponentAwaitCardThumb({ thumb }) {
  const { useWhere } = SnapshotWhere()
  const { data, error } = useAsync({ promiseFn: fetchPerson, thumb, useWhere })

  if (error) return <ComponentProductThumbNull />
  if (data) return <ComponentMapCardThumb {...{ data }} />
  return <ComponentSkeletonCardThumb />
}

export default ComponentAwaitCardThumb
