import styled from 'styled-components'

import { BorderRadius } from './root'

export const Div = styled.div`
  margin-bottom: ${({ marginBottom }) =>
    typeof marginBottom === 'number' ? `${marginBottom}px` : marginBottom};
  margin-left: ${({ marginLeft }) =>
    typeof marginLeft === 'number' ? `${marginLeft}px` : marginLeft};
  margin-right: ${({ marginRight }) =>
    typeof marginRight === 'number' ? `${marginRight}px` : marginRight};

  width: ${({ width }) => (typeof width === 'number' ? `${width}px` : width)};
  max-width: ${({ maxWidth }) =>
    typeof maxWidth === 'number' ? `${maxWidth}px` : maxWidth};
  min-width: ${({ minWidth }) =>
    typeof minWidth === 'number' ? `${minWidth}px` : minWidth};
  height: ${({ height }) =>
    typeof height === 'number' ? `${height}px` : height};
  max-height: ${({ maxHeight }) =>
    typeof maxHeight === 'number' ? `${maxHeight}px` : maxHeight};
  min-height: ${({ minHeight }) =>
    typeof minHeight === 'number' ? `${minHeight}px` : minHeight};

  display: ${({ displayFlex }) => displayFlex && 'flex'};

  justify-content: ${({ justifyContent }) => justifyContent && justifyContent};
  align-items: ${({ alignItems }) => alignItems && alignItems};
  flex-direction: ${({ flexDirection }) => flexDirection && flexDirection};

  & > * {
    width: ${({ widthChild }) =>
      widthChild && typeof widthChild === 'number'
        ? `${widthChild}px`
        : widthChild};
    height: ${({ heightChild }) =>
      heightChild && typeof heightChild === 'number'
        ? `${heightChild}px`
        : heightChild};
  }

  & > *:not(:last-child) {
    margin-top: ${({ spaceTopChild }) => spaceTopChild && spaceTopChild};
    margin-right: ${({ spaceRightChild }) =>
      spaceRightChild && spaceRightChild};
    margin-left: ${({ spaceLeftChild }) => spaceLeftChild && spaceLeftChild};
    margin-bottom: ${({ spaceBottomChild }) =>
      spaceBottomChild && spaceBottomChild};
  }

  & > * {
    display: flex;
  }

  & * {
    float: ${({ floatChild }) => floatChild && floatChild};
  }
`

export const Input = styled.div`
  width: 100%;
  display: flex;
  border: 1px solid #dadce0;

  ${BorderRadius(3)}
`

export const Child = styled.div`
  display: flex;
`
