import React, { useState } from 'react'
import { toast } from 'react-toastify'

import Bracael from '../Bracael'
import { SnapshotPost } from '../contexts/Confirma'
import { SnapshotStatus } from '../contexts/Cupom'

import {
  Container,
  Content,
  Main,
  Head,
  Title,
  Input,
  Button,
  Dsc,
  Footer,
  Readmore
} from '../styles/Cupom'

function ComponentCupomInsert() {
  const [useAnimate, setAnimate] = useState(false)
  const { useStatus, setStatus } = SnapshotStatus()
  const { setPost } = SnapshotPost()
  const { input_text = '' } = useStatus

  const isValid = (val) => {
    return /^[a-zA-Z]{1}[\w]*$/.test(val.toUpperCase()) || val === ''
  }

  const inputFn = (val) => {
    if (isValid(val)) {
      setStatus((prevState) =>
        Object({
          ...prevState,
          input_text: val.toUpperCase().trim()
        })
      )
    } else {
      setAnimate(true)
      setTimeout(() => setAnimate(false), 500)
    }
  }

  const confirmFn = () => {
    if (input_text) {
      setPost((prevState) =>
        Object({
          ...prevState,
          cupom_promotion: input_text
        })
      )
    } else {
      toast.error('Código de cupom obrigatório', Bracael.toast_options)
    }
  }

  return (
    <Container>
      <Content>
        <Main>
          <div>
            <Head>
              <Title>Cupom de desconto</Title>
            </Head>
            <Dsc>Possuí um código promocional?</Dsc>
          </div>
          <div>
            <Input
              type="text"
              placeholder="Digite seu código"
              status={useAnimate}
              onChange={(e) => inputFn(e.target.value)}
              value={input_text}
            />
          </div>
          <Footer>
            <Readmore>
              <svg
                height="16"
                viewBox="0 0 551.13 551.13"
                width="16"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="m275.565 0c-151.944 0-275.565 123.621-275.565 275.565s123.621 275.565 275.565 275.565 275.565-123.621 275.565-275.565-123.621-275.565-275.565-275.565zm0 516.685c-132.955 0-241.119-108.164-241.119-241.119s108.164-241.12 241.119-241.12 241.12 108.164 241.12 241.119-108.165 241.12-241.12 241.12z" />
                <path d="m258.342 413.348h34.446v34.446h-34.446z" />
                <path d="m275.565 103.337c-56.983 0-103.337 46.353-103.337 103.337v34.446h34.446v-34.446c0-37.995 30.897-68.891 68.891-68.891 37.995 0 68.891 30.897 68.891 68.891 0 25.851-13.775 50.188-35.943 63.492l-41.813 25.077c-5.18 3.112-8.359 8.712-8.359 14.767v68.891h34.446v-59.136l33.453-20.065c32.478-19.51 52.661-55.15 52.661-93.027.001-56.983-46.353-103.336-103.336-103.336z" />
              </svg>
              <span>Saiba mais</span>
            </Readmore>
            <Button type="button" onClick={() => confirmFn()}>
              Confirmar
            </Button>
          </Footer>
        </Main>
      </Content>
    </Container>
  )
}

export default ComponentCupomInsert
