import React, { useState } from 'react'

import { SnapshotBusiness } from '../contexts/App'
import { SnapshotPost } from '../contexts/Confirma'

import ComponentMethodDebit from './ComponentMethodDebit'
import ComponentMethodCredit from './ComponentMethodCredit'
import ComponentMethodVoucher from './ComponentMethodVoucher'

import {
  Main,
  Head,
  Title,
  Method,
  Sub,
  Mainly,
  Footer,
  Button
} from '../styles/Tarjeta'

function ComponentTarjetaMain({ close }) {
  const { usePost, setPost } = SnapshotPost()
  const { profile } = SnapshotBusiness()
  const { methods } = profile
  const { payment = {} } = usePost
  const { debit = [], credit = [], voucher = [] } = payment || {}
  const [useMethods, setMethods] = useState(
    JSON.stringify({ credit, debit, voucher })
  )

  const isValid = () => {
    return Object.entries(JSON.parse(useMethods))
      .map((data) => {
        const [id, values] = data
        const real = payment[id] || []
        return real.length < values.length
          ? values.map((val) => real.includes(val)).includes(false)
          : real.map((val) => values.includes(val)).includes(false)
      })
      .includes(true)
  }

  const saveFn = () => {
    close()
    setPost((prevState) => {
      const { cash = false, pix = false } = prevState.payment || {}
      return {
        ...prevState,
        payment: {
          cash,
          pix,
          ...JSON.parse(useMethods)
        }
      }
    })
  }

  return (
    <Main>
      <Head>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          height="18"
          width="18"
          viewBox="0 0 515.556 515.556"
        >
          <path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm32.222 386.667h-64.444v-64.444h64.444zm.002-96.667h-64.444v-161.111h64.444z" />
        </svg>
        <Title>Bandeiras a ser utilizadas!</Title>
      </Head>
      {Object.values(methods.debit || {}).includes(true) ? (
        <Method>
          <Sub>Cartão de Débito</Sub>
          <Mainly>
            {Object.entries(methods.debit || {}).map((item, i) => (
              <ComponentMethodDebit
                key={i}
                {...{ item, useMethods, setMethods }}
              />
            ))}
          </Mainly>
        </Method>
      ) : null}
      {Object.values(methods.credit || {}).includes(true) ? (
        <Method>
          <Sub>Cartão de Crédito</Sub>
          <Mainly>
            {Object.entries(methods.credit || {}).map((item, i) => (
              <ComponentMethodCredit
                key={i}
                {...{ item, useMethods, setMethods }}
              />
            ))}
          </Mainly>
        </Method>
      ) : null}
      {Object.values(methods.voucher || {}).includes(true) ? (
        <Method>
          <Sub>Voucher</Sub>
          <Mainly>
            {Object.entries(methods.voucher || {}).map((item, i) => (
              <ComponentMethodVoucher
                key={i}
                {...{ item, useMethods, setMethods }}
              />
            ))}
          </Mainly>
        </Method>
      ) : null}
      <Footer>
        <Button
          type="button"
          status={isValid()}
          onClick={() => isValid() && saveFn()}
        >
          Salvar
        </Button>
      </Footer>
    </Main>
  )
}

export default ComponentTarjetaMain
