import React from 'react'
import { useAsync } from 'react-async'
import { useParams } from 'react-router-dom'

import { SnapshotToken } from '../contexts/App'
import { functions } from '../services/server'

import ComponentEditId from '../components/ComponentEditId'
import ComponentSkeletonProductId from '../components/ComponentSkeletonProductId'

const repairUrl = (id) => {
  return id.indexOf('&') > -1 ? id.substring(id.indexOf('&'), 0) : id
}

const fetchPerson = async ({ token, id }) =>
  await functions()
    .httpsCallable('queryProduct')({ token, id: repairUrl(id) })
    .then((data) => data.data)

function EditId() {
  const { token } = SnapshotToken()
  const { id } = useParams()
  const { data, error } = useAsync({ promiseFn: fetchPerson, token, id })

  if (error) return error.message
  if (data) return <ComponentEditId {...{ data }} />
  return <ComponentSkeletonProductId />
}

export default EditId
