const { jQuery: $ } = window

export default {
  u: {
    a: [
      'Sunday',
      'Monday',
      'Tuesday',
      'Wednesday',
      'Thursday',
      'Friday',
      'Saturday'
    ],
    b: ['Domingo', 'Segunda', 'Terça', 'Quarta', 'Quinta', 'Sexta', 'Sábado'],
    c: [
      'Domingo',
      'Segunda-feira',
      'Terça-feira',
      'Quarta-feira',
      'Quinta-feira',
      'Sexta-feira',
      'Sábado'
    ],
    d: ['sun', 'mon', 'tue', 'wed', 'thu', 'fri', 'sat'],
    e: new Date().getDay()
  },
  category_routers: {
    '001': '/hamburguer',
    '002': '/porcoes',
    '003': '/bebidas',
    '004': '/combos',
    '005': '/promocoes',
    '006': '/bomboniere'
  },
  category_names: {
    '001': 'Hambúrguer',
    '002': 'Porções',
    '003': 'Bebidas',
    '004': 'Combos',
    '005': 'Promoções',
    '006': 'Bomboniere'
  },
  type_order: {
    1: {
      name: 'Entrega'
    },
    2: {
      name: 'Retirada no local'
    }
  },
  subtotal: function ({ detail, feed }) {
    return parseFloat(
      Number(
        detail
          .map(function (assoc) {
            const { id, amount = 1, additional = {} } = assoc

            const { price = 0, extra = {}, price_off = 0 } = feed[id] || {}

            return Number(
              amount * (price - (price * price_off) / 100) +
                Object.entries(extra)
                  .filter((data) => Object.keys(additional).includes(data[0]))
                  .map((data) => {
                    const uid = data[0]
                    const { value } = data[1]
                    const { amount = 1 } = additional[uid] || {}

                    return Number(value * amount)
                  })
                  .reduce((a, b) => a + b, 0)
            )
          })
          .reduce((a, b) => a + b, 0)
      ).toFixed(2)
    )
  },
  isValid: function ({
    usePost,
    data,
    useAmount,
    useComment,
    useSelector,
    useAdditional,
    useMoment,
    useCart
  }) {
    // eslint-disable-next-line no-unused-vars
    const { amount = 1, comment = '', additional = {}, selector = {} } = usePost
    // eslint-disable-next-line no-unused-vars
    const { pick = {} } = data

    // console.log(
    //   Object.entries(pick)
    //   .filter(data => data[1].important)
    // );
    // console.log(pick);

    const valSelector = () => {
      return Object.keys(selector).length
        ? Object.entries(selector)
            .map((data) => {
              const uid = data[0]
              const bag = data[1]
              const iBag = JSON.parse(useSelector)[uid] || []

              // bag (valor atual) é menor que iBag(valor em cache)
              return bag.length < iBag.length
                ? iBag.map((value) => bag.includes(value)).every((val) => !!val)
                : bag.map((value) => iBag.includes(value)).every((val) => !!val)
            })
            .includes(false)
        : false
    }

    const valExtra = () => {
      const isValid = (val) => {
        const uid = val[0]
        const { amount } = val[1]
        const { amount: iAmount = 0 } = JSON.parse(useAdditional)[uid] || {}

        return amount === iAmount
      }

      return Object.entries(additional).length ===
        Object.entries(JSON.parse(useAdditional)).length
        ? Object.entries(additional).length <
          Object.entries(JSON.parse(useAdditional)).length
          ? Object.entries(JSON.parse(useAdditional))
              .map(isValid)
              .includes(false)
          : Object.entries(additional).map(isValid).includes(false)
        : true
    }

    return (
      (Object.keys(pick).length
        ? Object.entries(pick)
            .map((data) => data[1].important)
            .includes(true)
          ? Object.entries(pick)
              .filter((data) => data[1].important)
              .map((val) => {
                const uid = val[0]
                const { important, limit } = val[1]
                const bag = selector[uid] || []

                return important && bag.length === limit
              })
              .every((val) => !!val)
          : true
        : true) &&
      (useAmount !== amount ||
        useComment !== comment ||
        valExtra() ||
        valSelector() ||
        !useCart.find((data) => data.moment === useMoment))
    )
  },
  formatOrder: function (data) {
    const { profile, delivery, order, feed, cupons } = data

    const span = '```'
    // eslint-disable-next-line no-unused-vars
    const { whatsapp } = profile

    const {
      comment = '',
      display_name,
      street,
      telephone_number,
      delivery_select,
      detail,
      house_number,
      complement,
      trade = 0,
      payment = {},
      order_type,
      cupom_promotion
    } = order
    const {
      district,
      rate = 0,
      zone
    } = order_type !== 2 ? delivery[delivery_select] : {}
    const { type_off = '001', value = 0 } = cupons[cupom_promotion] || {}

    const totalCart =
      type_off === '001'
        ? parseFloat(
            (
              this.subtotal({ detail, feed }) -
              (this.subtotal({ detail, feed }) * value) / 100
            ).toFixed(2)
          )
        : parseFloat((this.subtotal({ detail, feed }) + ~value + 1).toFixed(2))

    const comandCart = detail.map((assoc) => {
      const { id, amount = 1, comment, additional = {}, selector = {} } = assoc
      const { title, weight, grams, extra = {}, pick = {} } = feed[id]

      const lineComment = comment ? `\n└─ *“${comment}”*` : ''
      const lineExtra = Object.keys(extra).length
        ? Object.entries(extra)
            .filter((val) => Object.keys(additional).includes(val[0]))
            .map((val, i, all) => {
              const uid = val[0]
              const { name } = val[1]
              const { amount = 1 } = additional[uid] || {}

              return `\n${
                i === all.length - 1 && !lineComment ? '└─' : '├─'
              } _${amount} extra de ${name}_`
            })
            .join('')
        : ''

      const lineSelector = Object.keys(selector).length
        ? Object.entries(selector)
            .map((val) => {
              // eslint-disable-next-line no-unused-vars
              const [id, values] = val
              // eslint-disable-next-line no-unused-vars
              const { name, important, limit } = pick[id]
              // console.log(pick[id]);

              return `\n├─ _${name}_${values
                .map(
                  (data, i) =>
                    `\n├─── _${
                      important ? `${i + 1} de ${limit} ` : ''
                    }${data}_`
                )
                .join('')}`
            })
            .join('')
        : ''

      return `*${amount}x* ${span}${title}${
        weight
          ? this.unitSize({ ml: weight, space: true })
          : grams
          ? this.unitSize({ gm: grams, space: true })
          : ''
      }${span}${lineSelector}${lineExtra}${lineComment}`
    })

    const formatMethods = () => {
      const {
        cash = false,
        pix = false,
        debit = [],
        credit = [],
        voucher = []
      } = payment
      const user_method = []
      cash && user_method.push('Dinheiro')
      pix && user_method.push('Pix')
      debit.length && user_method.push('Débito')
      credit.length && user_method.push('Crédito')
      voucher.length && user_method.push('Voucher')

      return `${user_method.join(', ').replace(/, ([^,]*)$/, ' e $1')}`
    }

    return `*# DADOS PESSOAIS*
👤 _Nome:_ ${span}${display_name}${span}
📞 _Telefone:_ ${span}${telephone_number}${span}
${
  order_type !== 2
    ? `🌎 _Endereço:_ ${span}${street}, ${house_number}${
        complement ? `, ${complement}` : ''
      } - ${zone}${district ? ` - ${district}` : ''}${span}`
    : `🥡 _Endereço:_ ${span}Retirar no local${span}`
}

*# SOBRE O PEDIDO*
${comandCart.join('\n')}

*# SOBRE O PAGAMENTO*${comment ? `\n💬 _Comentário:_ ${comment}` : ''}
${`🤝🏻 _Forma de pagamento:_ ${formatMethods()}`}
${
  trade
    ? `💰 _Troco para:_ ${trade.toLocaleString('pt-BR', {
        style: 'currency',
        currency: 'BRL'
      })}\n`
    : ''
}${`💰 _Subtotal:_ ${this.subtotal({ detail, feed }).toLocaleString('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    })}\n`}${
      order_type !== 2
        ? `💰 _Taxa de entrega:_ ${rate.toLocaleString('pt-BR', {
            style: 'currency',
            currency: 'BRL'
          })}\n`
        : ''
    }${
      value
        ? `💰 _Desconto:_ ${
            type_off !== '001'
              ? Number(~value + 1).toLocaleString('pt-BR', {
                  style: 'currency',
                  currency: 'BRL'
                })
              : Number(
                  -Math.abs(this.subtotal({ detail, feed }) - totalCart)
                ).toLocaleString('pt-BR', {
                  style: 'currency',
                  currency: 'BRL'
                })
          } (${value}% OFF)\n`
        : ''
    }💰 *_TOTAL:_* ${span}${Number(totalCart + rate).toLocaleString('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    })}${span}`
  },
  unitSize: function ({ gm, ml, space = false, str = false }) {
    /*  :------------------------------------:
        | REALTIME DATABASE
        | gm = unidade de medida para gramas
        | ml = unidade de medida para mililitros
        | space = um espaço de uma letra inial
        | str = mostrar texto quando valor vazio
        :------------------------------------:  */

    // Peso indefinido : significa que o valor esperado não é um número, acontecerá devido algum erro ao subir o valor para o banco de dados

    return typeof gm === 'number' || typeof ml === 'number'
      ? ml === undefined
        ? gm >= 1000
          ? (function () {
              var countGrams = 0
              var atual = gm
              function decrementAgain() {
                countGrams = ++countGrams
                atual = atual - 1000
                // eslint-disable-next-line no-unused-expressions
                atual >= 1000 ? decrementAgain() : null
              }

              // eslint-disable-next-line no-unused-expressions
              atual >= 1000 ? decrementAgain() : null

              if (atual !== 0) {
                return space
                  ? ` ${Number(countGrams + '.' + atual)
                      .toFixed(1)
                      .replace('.', ',')}kg`
                  : `${Number(countGrams + '.' + atual)
                      .toFixed(1)
                      .replace('.', ',')}kg`
              } else {
                return space ? ` ${countGrams}kg` : `${countGrams}kg`
              }
            })()
          : gm !== 0
          ? space
            ? ` ${gm}g`
            : ` ${gm}g`
          : str
          ? 'Peso não informado'
          : ''
        : ml >= 1000
        ? (function () {
            var countGrams = 0
            var atual = ml

            function decrementAgain() {
              countGrams = ++countGrams
              atual = atual - 1000
              // eslint-disable-next-line no-unused-expressions
              atual >= 1000 ? decrementAgain() : null
            }

            // eslint-disable-next-line no-unused-expressions
            atual >= 1000 ? decrementAgain() : null

            if (atual !== 0) {
              return space
                ? ` ${Number(countGrams + '.' + atual)
                    .toFixed(1)
                    .replace('.', ',')}L`
                : `${Number(countGrams + '.' + atual)
                    .toFixed(1)
                    .replace('.', ',')}L`
            } else {
              return space ? ` ${countGrams}L` : `${countGrams}L`
            }
          })()
        : space
        ? ` ${ml}ml`
        : `${ml}ml`
      : str
      ? 'Peso indefinido'
      : ''
  },
  btnAnimate: function ({ first, second, action, bolean = true }) {
    const INCREMENTdiv = bolean ? first : second
    const GETTINGdiv = bolean ? second : first

    $(INCREMENTdiv).animate(
      { opacity: 0 },
      {
        duration: 400,
        easing: 'linear',
        queue: false
      }
    )

    $(INCREMENTdiv).animate(
      {
        transform: '26'
      },
      {
        step: function (now) {
          $(this).css({ transform: 'translate3d(0px, ' + now + 'px, 0px)' })
        },
        duration: 400,
        easing: 'linear',
        queue: false,
        complete: function () {
          // action()
          // this.removeAttribute('style')
        }
      },
      'linear'
    )

    setTimeout(function () {
      $(GETTINGdiv).fadeIn(400, function () {
        action()
        GETTINGdiv.removeAttribute('style')
        INCREMENTdiv.removeAttribute('style')
      })
    }, 200)
  }
}
