import React, { useRef, useEffect } from 'react'

import { SnapshotSwipe } from '../contexts/App'

import { Swipe } from '../styles/Swipe'

const { jQuery: $ } = window

function ComponentSwipe() {
  const useStatus = useRef(false)
  const useSucessLayout = useRef(null)

  // eslint-disable-next-line no-unused-vars
  const { useSwipe, setSwipe } = SnapshotSwipe()

  useEffect(() => {
    if (useSucessLayout.current && useSwipe) {
      $(useSucessLayout.current).fadeIn(200)
      useStatus.current = false
    }
  }, [useSwipe])

  useEffect(() => {
    if (useStatus.current) {
      if (!useSwipe) {
        $(useSucessLayout.current).fadeOut(300)
      }
    } else {
      useStatus.current = true
    }
  }, [useSwipe])

  return <Swipe ref={useSucessLayout} onClick={() => setSwipe(false)} />
}

export default ComponentSwipe
