import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import ReactDOM from 'react-dom'

import Bracael from '../Bracael'
import { SnapshotBag, SnapshotCart } from '../contexts/App'

import ComponentAwaitCartThumb from './ComponentAwaitCartThumb'
import ComponentCartThumbNull from './ComponentCartThumbNull'
import ComponentModalEcart from './ComponentModalEcart'

import {
  Info,
  Head,
  Price,
  Count,
  Center,
  Controls,
  Button
} from '../styles/Cart'

const { jQuery: $ } = window

function ComponentMapCart({ data, i, feed }) {
  const history = useHistory()
  const componentFn = (() => {
    const Root = document.createElement('div')
    Root.setAttribute('role', 'modal')
    return Root
  })()

  const { setBag } = SnapshotBag()
  const { useCart, setCart } = SnapshotCart()
  const [useModal, setModal] = useState(false)
  const [useHref, setHref] = useState(false)
  const [useRemove, setRemove] = useState(false)

  const { id, amount = 1, comment = '', additional = {} } = data
  const {
    title,
    weight,
    grams,
    price,
    thumb = [],
    extra = {},
    price_off = 0
  } = feed[id]

  const ReduceExtra = Object.keys(additional).length
    ? Object.entries(additional)
        .map((val) => {
          const uid = val[0]
          const { amount } = val[1]
          const { value } = extra[uid]

          return Number(value * amount)
        })
        .reduce((a, b) => a + b, 0)
    : 0

  const buttonFn = () => {
    ReactDOM.render(
      <ComponentModalEcart
        {...{ setModal, setRemove, setHref, title, amount, id }}
      />,
      document.body.appendChild(componentFn)
    )
  }

  useEffect(() => {
    if (useModal) {
      $('[role="modal"]').fadeOut(200, function () {
        this.remove()
      })
      setModal(false)
    }
  }, [history, useModal])

  useEffect(() => {
    if (useHref) {
      $('[role="modal"]').fadeOut(400, function () {
        this.remove()
        history.push(`/p/${id}/${i}`)
        setHref(false)
        setBag(false)
      })
    }
  }, [useHref])

  useEffect(() => {
    if (useRemove) {
      $('[role="modal"]').fadeOut(400, function () {
        this.remove()
        const assoc = [...useCart]
        assoc.splice(assoc.map((data) => data.id).indexOf(id), 1)
        setCart(assoc)
      })
    }
  }, [useRemove])

  return (
    <div>
      {thumb.length ? (
        <ComponentAwaitCartThumb {...{ thumb }} />
      ) : (
        <ComponentCartThumbNull />
      )}
      <Info>
        <Head>
          <Price>
            {Number(
              amount * (price - (price * price_off) / 100) + ReduceExtra
            ).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
          </Price>
          <Controls>
            {ReduceExtra ? (
              <Center>
                {ReduceExtra ? (
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="#e47a2e"
                  >
                    <path
                      fillRule="evenodd"
                      data-name="Plus"
                      d="M14,10H10v4a2,2,0,0,1-4,0V10H2A2,2,0,1,1,2,6H6V2a2,2,0,1,1,4,0V6h4A2,2,0,0,1,14,10Z"
                    />
                  </svg>
                ) : null}
              </Center>
            ) : null}
            {comment ? (
              <Center>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="#e47a2e"
                >
                  <path
                    fillRule="evenodd"
                    data-name="Comment"
                    d="M14,12H7L3,16V12H2a2,2,0,0,1-2-2V2A2,2,0,0,1,2,0H14a2,2,0,0,1,2,2v8A2,2,0,0,1,14,12ZM3,3H13V5H3V3ZM3,7H13V9H3V7Z"
                  />
                </svg>
              </Center>
            ) : null}
            {amount > 1 ? <Count>{amount}</Count> : null}
            <Button type="button" onClick={() => buttonFn()}>
              <svg
                viewBox="0 0 515.555 515.555"
                xmlns="http://www.w3.org/2000/svg"
                width="18"
                height="18"
                fill="#777777"
              >
                <path d="m303.347 18.875c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0"></path>
                <path d="m303.347 212.209c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0"></path>
                <path d="m303.347 405.541c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0"></path>
              </svg>
            </Button>
          </Controls>
        </Head>
        <span>{title}</span>
        <span>
          {weight !== undefined
            ? Bracael.unitSize({ ml: weight, str: true })
            : Bracael.unitSize({ gm: grams, str: true })}
        </span>
      </Info>
    </div>
  )
}

export default ComponentMapCart
