import React, { useEffect } from 'react'

import { Close, Content } from '../styles/Modal'
import {
  Main,
  Head,
  Headly,
  Title,
  Subtitle,
  Text,
  Footer,
  Button,
  Cancel
} from '../styles/Ecart'

const { jQuery: $ } = window

function ComponentModalEcart({ setModal, setRemove, setHref, title, amount }) {
  const closeFn = () => setModal(true)

  useEffect(() => {
    if (document.querySelector('[role="modal"]')) {
      $('[role="modal"]').fadeIn(200)
    }
  }, [])

  return (
    <>
      <Close onClick={() => closeFn()} />
      <Content zIndex="4" alignItems="center">
        <Main>
          <Head>
            <Headly>
              <Title>{title}</Title>
            </Headly>
            <Subtitle>
              <Text>{amount}un.</Text>
            </Subtitle>
          </Head>
          <Footer>
            <Button
              type="button"
              color="#E54B40"
              onClick={() => setRemove(true)}
            >
              <span>Remover</span>
            </Button>
            <Button type="button" onClick={() => setHref(true)}>
              <span>Editar item</span>
            </Button>
          </Footer>
          <Cancel type="button" onClick={() => closeFn()}>
            <span>Cancelar</span>
          </Cancel>
        </Main>
      </Content>
    </>
  )
}

export default ComponentModalEcart
