import React, { createContext, useContext } from 'react'

const ElementContext = createContext()

export default function ElementProvider({
  children,
  useTrade,
  setTrade,
  useTarjeta,
  setTarjeta
}) {
  return (
    <ElementContext.Provider
      value={{ useTrade, setTrade, useTarjeta, setTarjeta }}
    >
      {children}
    </ElementContext.Provider>
  )
}

export function SnapshotTrade() {
  const context = useContext(ElementContext)
  const { useTrade, setTrade } = context
  return { useTrade, setTrade }
}

export function SnapshotTarjeta() {
  const context = useContext(ElementContext)
  const { useTarjeta, setTarjeta } = context
  return { useTarjeta, setTarjeta }
}
