import React, { useState, useRef } from 'react'
import { toast } from 'react-toastify'

import Bracael from '../Bracael'
import {
  SnapshotWhere,
  SnapshotCart,
  SnapshotBusiness,
  SnapshotService
} from '../contexts/App'
import {
  SnapshotScreen,
  SnapshotPost,
  SnapshotSucess,
  SnapshotModal,
  SnapshotCupom
} from '../contexts/Confirma'
import ElementProvider from '../contexts/Submit'

import ComponentConfirmaPayment from './ComponentConfirmaPayment'
import ComponentConfirmaTarjeta from './ComponentConfirmaTarjeta'
import ComponentConfirmaTrade from './ComponentConfirmaTrade'

import {
  Container,
  Main,
  Title,
  MSG,
  SUB,
  Column,
  Textarea,
  Italic,
  Footer,
  OU,
  Simple,
  Button
} from '../styles/Submit'

function ComponentConfirmSubmit({ data }) {
  const { delivery, feed } = data
  const clickRef = useRef(true)
  const [useTrade, setTrade] = useState({})
  const [useTarjeta, setTarjeta] = useState({})
  const [useAttempts, setAttempts] = useState(0)
  const { setModal } = SnapshotModal()
  const { setService } = SnapshotService()
  const { setSucess } = SnapshotSucess()
  const { useWhere } = SnapshotWhere()
  const { setCart } = SnapshotCart()
  const { setScreen } = SnapshotScreen()
  const { useCupom } = SnapshotCupom()
  const { profile } = SnapshotBusiness()
  const { usePost, setPost } = SnapshotPost()
  const { comment = '', payment = {}, cupom_promotion } = usePost

  const inputTextarea = (val) =>
    setPost((prevState) =>
      Object({
        ...prevState,
        comment: val
      })
    )

  async function formSubmit() {
    return await fetch(
      'https://us-central1-delivery-bracael.cloudfunctions.net/checkout',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json'
        },
        body: JSON.stringify({ where: useWhere, data: usePost })
      }
    ).then((response) => response.json())
  }

  const cupons = cupom_promotion
    ? Object({ [cupom_promotion]: { ...useCupom } })
    : {}

  const altSubmit = () => {
    var windowReference = window.open()
    const { whatsapp } = profile

    if (clickRef.current) {
      clickRef.current = false
      setModal({ status: true })
      formSubmit()
        .then((data) => {
          const { status, id } = data

          if (status) {
            setSucess({
              status,
              id,
              url: `https://api.whatsapp.com/send?phone=55${whatsapp.replace(
                /[^0-9]/g,
                ''
              )}&text=${window.encodeURIComponent(
                Bracael.formatOrder({
                  profile,
                  delivery,
                  order: usePost,
                  feed,
                  cupons
                })
              )}`
            })
            setCart([])
            setService((prevSate) => [...prevSate, id])
            toast.success('Seu pedido foi enviado com sucesso!', {
              position: 'top-right',
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined
            })
            windowReference.location = `https://api.whatsapp.com/send?phone=55${whatsapp.replace(
              /[^0-9]/g,
              ''
            )}&text=${window.encodeURIComponent(
              Bracael.formatOrder({
                profile,
                delivery,
                order: usePost,
                feed,
                cupons
              })
            )}`
          } else {
            if (useAttempts < 3) {
              toast.error(
                'Algo saiu mal, tente novamente!',
                Bracael.toast_options
              )
              setAttempts((prevState) => prevState + 1)
            } else {
              toast.error(
                'Se este erro persistir, contate o fornecedor!',
                Bracael.toast_options
              )
            }
          }

          clickRef.current = true
          setModal({ status: false })
        })
        .catch(() => {
          windowReference.close()
          toast.error('Oops, erro! atualize a página e tente novamente...', {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined
          })
          clickRef.current = true
          setModal({ status: false })
        })
    }
  }

  const isValidStatus = () => {
    const {
      cash = false,
      pix = false,
      debit = [],
      credit = [],
      voucher = []
    } = payment || {}
    return cash || pix || debit.length || credit.length || voucher.length
  }

  const isValid = () => {
    const isFalse = () => {
      toast.warn('Selecione uma forma de pagamento...', {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      })
      return false
    }

    return isValidStatus() ? true : isFalse()
  }

  return (
    <Container>
      <ElementProvider {...{ useTrade, setTrade, useTarjeta, setTarjeta }}>
        <Main>
          <Title>Forma de pagamento</Title>
          <MSG>
            Informe o método de pagamento para que tenhamos uma entrega rápida e
            nosso atendimento seja eficaz. A entrega melhora muito com alguns
            detalhes importante.
          </MSG>
          <Column>
            <ComponentConfirmaPayment />
          </Column>
          <SUB>Mais alguma coisa?</SUB>
          <Textarea
            placeholder="Ex. interphone está com defeito"
            onChange={(e) => inputTextarea(e.target.value)}
            value={comment}
          ></Textarea>
          <Italic>
            Tenha o WhatsApp instalado em seu Smarthphone para prosseguir com o
            envio do pedido
          </Italic>
          <Footer>
            <Button
              type="button"
              useAfter={true}
              status={isValidStatus()}
              onClick={() => isValid() && altSubmit()}
            >
              Enviar pedido!
            </Button>
            <OU>
              <span>ou</span>
            </OU>
            <Simple type="button" onClick={() => setScreen(2)}>
              Voltar atrás
            </Simple>
          </Footer>
        </Main>
        <ComponentConfirmaTrade />
        <ComponentConfirmaTarjeta />
      </ElementProvider>
    </Container>
  )
}

export default ComponentConfirmSubmit
