import React from 'react'
import Skeleton from 'react-loading-skeleton'

import { Div } from '../styles/Skeleton'
import { Li, L, R } from '../styles/Bairros'

function ComponentSkeletonMapBairros({ data }) {
  const { initial, zone } = data

  return (
    <Li>
      <L style={{ height: '20px' }}>
        {initial ? (
          <Div displayFlex={true} floatChild="left">
            <Skeleton width={10} height={8} />
          </Div>
        ) : (
          <Div displayFlex={true} style={{ width: '10px', height: '9px' }} />
        )}
        <Div displayFlex={true} floatChild="left">
          <Skeleton width={zone} height={8} />
        </Div>
      </L>
      <R style={{ height: '20px' }}>
        <Div displayFlex={true} alignItems="center" floatChild="left">
          <Skeleton width={50} height={11} />
        </Div>
      </R>
    </Li>
  )
}

export default ComponentSkeletonMapBairros
