import React from 'react'

import { SnapshotBusiness } from '../contexts/App'
import { SnapshotPost } from '../contexts/Confirma'

import ComponentConfirmaCupom from './ComponentConfirmaCupom'

import { Content, Main, Choose, Texts, Name, Subtxt } from '../styles/Start'

function ComponentConfirmaStart() {
  const { setPost } = SnapshotPost()

  const { accessibility } = SnapshotBusiness()
  const { cupom_status = false } = accessibility

  const buttonFn = (val) =>
    setPost((prevState) =>
      Object({
        ...prevState,
        order_type: val
      })
    )

  return (
    <Content>
      {cupom_status && <ComponentConfirmaCupom />}
      <Main>
        <Choose onClick={() => buttonFn(1)}>
          <Texts>
            <Name>Entrega</Name>
            <Subtxt>(sujeito a disponibilidade e taxas)</Subtxt>
          </Texts>
          <div>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              height="24"
              width="24"
              viewBox="0 0 24 24"
              fill="#fff"
            >
              <g>
                <path fill="none" d="M0 0h24v24H0z"></path>
                <path d="M13.172 12l-4.95-4.95 1.414-1.414L16 12l-6.364 6.364-1.414-1.414z"></path>
              </g>
            </svg>
          </div>
        </Choose>
        <Choose onClick={() => buttonFn(2)}>
          <Texts>
            <Name>Retirada no local</Name>
            <Subtxt>(sujeito a disponibilidade)</Subtxt>
          </Texts>
          <div>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              height="24"
              width="24"
              viewBox="0 0 24 24"
              fill="#fff"
            >
              <g>
                <path fill="none" d="M0 0h24v24H0z"></path>
                <path d="M13.172 12l-4.95-4.95 1.414-1.414L16 12l-6.364 6.364-1.414-1.414z"></path>
              </g>
            </svg>
          </div>
        </Choose>
      </Main>
    </Content>
  )
}

export default ComponentConfirmaStart
