import React from 'react'
import { useAsync } from 'react-async'

import { SnapshotToken } from '../contexts/App'
import { functions } from '../services/server'

import ComponentBairros from './ComponentBairros'
import ComponentSkeletonBairros from './ComponentSkeletonBairros'

const fetchPerson = async ({ token }) =>
  await functions()
    .httpsCallable('queryObject')({ token, ref: 'business/delivery' })
    .then((data) => data.data)

function ComponentAwaitBairros() {
  const { token } = SnapshotToken()
  const { data, error } = useAsync({ promiseFn: fetchPerson, token })

  if (error) return error.message
  if (data) return <ComponentBairros {...{ data }} />
  return <ComponentSkeletonBairros />
}

export default ComponentAwaitBairros
