import React, { useEffect, useRef } from 'react'

import { SnapshotPost } from '../contexts/Confirma'
import { SnapshotTarjeta } from '../contexts/Submit'

import { Content, Main, Value, To } from '../styles/Payment'

const { jQuery: $ } = window

function ComponentConfirmaCard({ useCard }) {
  const statusRef = useRef(false)
  const methodRef = useRef(null)
  const { setTarjeta } = SnapshotTarjeta()
  const { usePost, setPost } = SnapshotPost()
  const { payment } = usePost
  const { credit = [], debit = [], voucher = [] } = payment || {}

  const editFn = () => setTarjeta({ status: true })

  useEffect(() => {
    const { current } = methodRef
    if (statusRef.current || useCard) {
      $(current).animate({ height: 'toggle' }, 300, function () {
        if (!useCard) {
          setPost((prevState) => {
            // NOTE: O uso do assoc aqui, talvez não seja a melhor opção
            const assoc = { ...prevState }
            delete assoc.credit
            delete assoc.debit
            delete assoc.voucher
            const { ...rest_payment } = assoc.payment || {}

            return {
              ...assoc,
              credit: {},
              debit: {},
              voucher: {},
              payment: {
                ...rest_payment
              }
            }
          })
        }
      })
      statusRef.current = true
    } else {
      statusRef.current = true
    }
  }, [useCard])

  return (
    <Content ref={methodRef}>
      <Main>
        <Value>
          {[...debit, ...credit, ...voucher].length
            ? [...debit, ...credit, ...voucher].length > 1
              ? `${
                  [...debit, ...credit, ...voucher].length
                } cartões selecionado!`
              : '1 cartão selecionado'
            : 'Nenhum cartão selecionado'}
        </Value>
        <To type="button" onClick={() => editFn()}>
          Editar
        </To>
      </Main>
    </Content>
  )
}

export default ComponentConfirmaCard
