import React, { useEffect, useRef } from 'react'

import { SnapshotOrder, SnapshotComanda } from '../contexts/Service'

import ComponentComandaMain from './ComponentComandaMain'

import { Modal, Close, Content } from '../styles/Modal'

const { jQuery: $ } = window

function ComponentModalComanda() {
  const modalRef = useRef(null)
  const statusonRef = useRef(false)
  const statusoffRef = useRef(false)
  const { useComanda, setComanda } = SnapshotComanda()
  const { useOrder } = SnapshotOrder()
  const { id } = useComanda

  useEffect(() => {
    const { status } = useComanda
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200)
    } else {
      statusonRef.current = true
    }
  }, [statusonRef, modalRef, useComanda])

  useEffect(() => {
    const { status } = useComanda
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setComanda({}))
    } else {
      statusoffRef.current = true
    }
  }, [useComanda])

  const cancenlFn = () =>
    setComanda((prevState) => Object({ ...prevState, status: false }))

  return (
    <Modal ref={modalRef} backgroundColor="rgba(0,0,0,0.5)" zIndex="6">
      {useOrder[useOrder.map((data) => data.id).indexOf(id)] ||
      useComanda.status === false ? (
        <>
          <Close onClick={() => cancenlFn()} />
          <Content zIndex="4" alignItems="center">
            <ComponentComandaMain {...{ id }} />
          </Content>
        </>
      ) : null}
    </Modal>
  )
}

export default ComponentModalComanda
