import React from 'react'
import pt from 'date-fns/locale/pt-BR'
import { format } from 'date-fns'

import { SnapshotCupom, SnapshotPost } from '../contexts/Confirma'
import { SnapshotStatus } from '../contexts/Cupom'

import * as S from '../styles/Cupom'
import {
  Container,
  Content,
  Header,
  OFF,
  Value,
  Porcent,
  Side,
  Hash,
  Help,
  Expr,
  Footer,
  Main,
  Title,
  Stitle,
  Valid,
  Cancel
} from '../styles/CupomThen'

function ComponentCupomThen() {
  const { usePost, setPost } = SnapshotPost()
  const { useCupom, setCupom } = SnapshotCupom()
  const { setStatus } = SnapshotStatus()
  const { cupom_promotion } = usePost

  const { title, description, validate, value } = useCupom

  const handleConfirm = () => {
    setCupom({})
    setStatus({})
    const assoc = { ...usePost }
    delete assoc.cupom_promotion
    setPost(assoc)
  }

  return (
    <S.Container>
      <Container>
        <Content>
          <Header>
            <OFF>
              <Value>{value}%</Value>
              <Porcent>OFF</Porcent>
            </OFF>
          </Header>
          <Side>
            <Hash>{cupom_promotion}</Hash>
            {!!description && (
              <div>
                <Help type="button" onClick={() => alert(description)}>
                  <svg
                    height="16"
                    viewBox="0 0 551.13 551.13"
                    width="16"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#999"
                  >
                    <path d="m275.565 0c-151.944 0-275.565 123.621-275.565 275.565s123.621 275.565 275.565 275.565 275.565-123.621 275.565-275.565-123.621-275.565-275.565-275.565zm0 516.685c-132.955 0-241.119-108.164-241.119-241.119s108.164-241.12 241.119-241.12 241.12 108.164 241.12 241.119-108.165 241.12-241.12 241.12z" />
                    <path d="m258.342 413.348h34.446v34.446h-34.446z" />
                    <path d="m275.565 103.337c-56.983 0-103.337 46.353-103.337 103.337v34.446h34.446v-34.446c0-37.995 30.897-68.891 68.891-68.891 37.995 0 68.891 30.897 68.891 68.891 0 25.851-13.775 50.188-35.943 63.492l-41.813 25.077c-5.18 3.112-8.359 8.712-8.359 14.767v68.891h34.446v-59.136l33.453-20.065c32.478-19.51 52.661-55.15 52.661-93.027.001-56.983-46.353-103.336-103.336-103.336z" />
                  </svg>
                </Help>
              </div>
            )}
          </Side>
        </Content>
        <Main>
          <Title>{title}</Title>
        </Main>
        <Footer>
          <Valid>
            <Stitle>válido {validate ? 'até' : 'por'}</Stitle>
            <Expr>
              {validate
                ? `válido até ${format(
                    new Date(validate),
                    "dd 'de' MMMM 'de' yyyy",
                    { locale: pt }
                  )}`
                : 'Tempo inderteminado'}
            </Expr>
          </Valid>
          <Cancel type="button" onClick={() => handleConfirm()}>
            Cancelar
          </Cancel>
        </Footer>
      </Container>
    </S.Container>
  )
}

export default ComponentCupomThen
