import React from 'react'
import { useAsync } from 'react-async'

import { getImageUrl } from '../app/index'

import { SnapshotWhere } from '../contexts/App'
import { storage } from '../services/server'

import ComponentCartThumb from './ComponentCartThumb'
import ComponentSkeletonCartThumb from './ComponentSkeletonCartThumb'
import ComponentCartThumbNull from './ComponentCartThumbNull'

const awaitStorage = async (data, where) => {
  const { name } = data

  return await storage()
    .ref(getImageUrl({ where, ref: `${name}_160x160.jpeg` }))
    .getDownloadURL()
}

const fetchPerson = async ({ thumb, useWhere }) => {
  const [image] = thumb
  return await awaitStorage(image, useWhere)
}

function ComponentAwaitCartThumb({ thumb }) {
  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere()
  const { data, error } = useAsync({ promiseFn: fetchPerson, thumb, useWhere })

  if (error) return <ComponentCartThumbNull />
  if (data) return <ComponentCartThumb {...{ data }} />
  return <ComponentSkeletonCartThumb />
}

export default ComponentAwaitCartThumb
