import React from 'react'
import { useAsync } from 'react-async'

import Bracael from '../Bracael'
import { functions } from '../services/server'
import { SnapshotToken } from '../contexts/App'

import ComponentCards from './ComponentCards'
import ComponentSkeletonCards from './ComponentSkeletonCards'

const fetchPerson = async ({ token }) =>
  await functions()
    .httpsCallable('queryObject')({ token, ref: 'feed' })
    .then((data) => data.data)

function ComponentAwaitCards({ configGlobal }) {
  const { token } = SnapshotToken()
  const { data, error } = useAsync({ promiseFn: fetchPerson, token })

  const { id_feed } = configGlobal
  const organize = (a, b) =>
    a[1].date_create < b[1].date_create
      ? 1
      : b[1].date_create < a[1].date_create
      ? -1
      : 0
  const division = (data) => {
    const [, assoc] = data

    return (
      (assoc.category === '005'
        ? assoc.category === id_feed &&
          (assoc.available || {})[Bracael.u.d[new Date().getDay()]]
        : assoc.category === id_feed) &&
      ((assoc.status || 200) === 200 || (assoc.status || 200) === 403)
    )
  }

  if (error) return error.message
  if (data)
    return (
      <ComponentCards
        {...{
          data: Object.entries(data).filter(division).sort(organize),
          configGlobal
        }}
      />
    )
  return <ComponentSkeletonCards />
}

export default ComponentAwaitCards
