import React from 'react'
import { useLocation, useHistory } from 'react-router-dom'

import Bracael from '../Bracael'
import { SnapshotBag, SnapshotCart } from '../contexts/App'

import ComponentMapCart from './ComponentMapCart'

import { Inner, Title, Value, List, Confirm } from '../styles/Cart'

function ComponentCartContent({ feed }) {
  const { pathname } = useLocation()
  const history = useHistory()

  const { setBag } = SnapshotBag()
  const { useCart } = SnapshotCart()

  const ReduceCart = Bracael.subtotal({ detail: useCart, feed })

  const goConfirm = () => {
    history.push('/confirma')
    setBag(false)
  }

  return (
    <Inner>
      <Title>Todos os seus pedidos</Title>
      <Value>
        <strong>subtotal a pagar</strong>
        <p>
          <span>R$</span>
          {Number(ReduceCart)
            .toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
            .replace('R$', '')
            .trim()}
        </p>
      </Value>
      <List>
        <div>
          {useCart.map((data, i) => (
            <ComponentMapCart key={data.moment} {...{ data, i, feed }} />
          ))}
        </div>
      </List>
      {pathname !== '/confirma' && (
        <Confirm>
          <button type="button" onClick={() => goConfirm()}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              viewBox="0 0 78.369 78.369"
              fill="#fff"
            >
              <path d="M78.049,19.015L29.458,67.606c-0.428,0.428-1.121,0.428-1.548,0L0.32,40.015c-0.427-0.426-0.427-1.119,0-1.547l6.704-6.704 c0.428-0.427,1.121-0.427,1.548,0l20.113,20.112l41.113-41.113c0.429-0.427,1.12-0.427,1.548,0l6.703,6.704 C78.477,17.894,78.477,18.586,78.049,19.015z"></path>
            </svg>
            <span>Confirmar</span>
          </button>
        </Confirm>
      )}
    </Inner>
  )
}

export default ComponentCartContent
