import React from 'react'
import { useAsync } from 'react-async'

import { functions } from '../services/server'
import { SnapshotToken } from '../contexts/App'

import ComponentText from './ComponentText'
import ComponentSkeletonText from './ComponentSkeletonText'

const fetchPerson = async ({ token }) =>
  await functions()
    .httpsCallable('queryObject')({ token, ref: 'business/container/politics' })
    .then((data) => data.data)

function ComponentAwaitPolitica() {
  const { token } = SnapshotToken()
  const { data, error } = useAsync({ promiseFn: fetchPerson, token })

  if (error) return error.message
  if (data) return <ComponentText {...{ data }} />
  return <ComponentSkeletonText />
}

export default ComponentAwaitPolitica
